import {  InputPatterns } from "../../_models/AppConstants";

export class ValidationService {

    public static ifEmailMatchPattern(email: string) {
        let regexp = new RegExp(InputPatterns.EMAIL_PATTERN);
        let res = regexp.test(email);
        return res;
    }

    public static ifPasswordMatchPattern(password: string) {
        let regexp = new RegExp(InputPatterns.PASSWORD_PATTERN);
        let res = regexp.test(password);
        return res;
    }

    public static ifNameMatchPattern(nameOrUserName: string) {
        let regexp = new RegExp(InputPatterns.USER_NAME_PATTERN);
        return regexp.test(nameOrUserName);
    }
}