import { Component } from '@angular/core';
import { Router, NavigationStart, NavigationEnd } from '@angular/router';
import { RouteHandlerService } from './_services/route-handler.service'
import { AppComponentViewModel } from './_models/app-component/viewModels';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ContentHelper } from './_services/helpers/contentHelper';

enum NavMenuType {
  BASIC = "BASIC",
  STARNDART = "STARNDART",
}

enum FooterType {
  FOOTER_THEME_WHITE_BACKGROUND = "WHITE_BACKGROUND",
  FOOTER_THEME_PRIMARY_BACKGROUND = "PRIMARY_BACKGROUND",
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html'
})

export class AppComponent {
  private contentHandlerService: RouteHandlerService = new RouteHandlerService();
  public appComponentViewModel: AppComponentViewModel;

  constructor(
    private _router: Router) {
    this.appComponentViewModel = this.contentHandlerService.getViewModel();
  }

  ngOnInit() {
    this._router.events.subscribe((route) => {
      if (route instanceof NavigationStart) {
        window.scroll({ 
          top: 0, 
          left: 0, 
          behavior: 'smooth' 
        });

      }
      if (route instanceof NavigationEnd) {
        this.contentHandlerService.handleContentByRoute(route.url);
        ContentHelper.setLoading(false);
      }
    });
  }

  closeModalPrivacy() {
    var body = document.querySelector('body');
    body.classList.remove('modal-open-privacy');
  }


}