import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../_services/api/api.service';
import { User } from 'src/app/_models/user/user';
import { AppConstants, HealthCheckResult } from 'src/app/_models/AppConstants';
import { Router } from '@angular/router';
import { QRCodeService } from 'src/app/_services/qrCode.service';
import { ToastrManager } from 'ng6-toastr-notifications';
import { LocalStorageService } from 'src/app/_services/localStorage.service';
import { Child } from 'src/app/_models/children/child';
import { ApiBaseRequests } from 'src/app/_services/helpers/apiBaseRequests';
import { GetChecksResppnseModel } from 'src/app/_models/api/responseModel/getChecksResponseModel';
import { Check } from 'src/app/_models/health-test/HealthCheckModel';

@Component({
  selector: 'my-result',
  templateUrl: './my-result.component.html',
  styleUrls: ['my-result.component.scss']
})
export class MyResultComponent implements OnInit {

  private user: User;
  public checks: Array<Check> = [];
  public allUserChecks: Array<Check> = [];
  public monthes: any = AppConstants.monthModels;
  public selectedMonthId: number;
  public selectedIdentity: any;
  public loading: boolean = false;
  public cardLoading: boolean = false;
  public haveNoResults: boolean = false;

  public identities: Array<any> = [];
  public currentIdentitiy: any = {};

  public identityDropdownModel: any;
  public hasChildren: boolean = false;

  constructor(private apiService: ApiService,
    private _router: Router,
    private toastr: ToastrManager,
    private localStorageService: LocalStorageService,
    private apiBaseRequests: ApiBaseRequests) {
  }

  ngOnInit(): void {
    this.loading = true;

    this.user = this.localStorageService.getUser();
    if (!this.user) {
      this._router.navigate(['/login']);
      return;
    }

    this.initIdentities()

    this.identityDropdownModel = this.user.id;
    this.getChecksByIdentity(this.user.id);
  }

  initIdentities() {
    this.identities = new Array<any>();
    this.identities.push(this.mapDropdownIdentityModel(this.user.id, this.user.firstName, this.user.lastName));

    let children: Array<Child> = this.localStorageService.getChildren();
    if (!children || children.length == 0) {
      this.hasChildren = false;
      return;
    }
    this.hasChildren = true;

    children.forEach(item => {
      this.identities.push(this.mapDropdownIdentityModel(item.id, item.firstName, item.lastName));
    })

  }

  private mapDropdownIdentityModel(id: string, firstName: string, lastName: string) {
    let identity: any = {};
    identity.id = id;
    identity.firstName = firstName;
    identity.lastName = lastName;
    return identity;
  }

  showDefaultResult() {
    this.haveNoResults = false;

    this.checks = this.allUserChecks.slice(0, 20);
    if (this.checks.length == 0)
      this.haveNoResults = true;
  }

  sortResults(month: number) {
    this.haveNoResults = false;

    this.loading = true;

    if (!month) {
      this.showDefaultResult();
      return;
    }

    this.checks = this.allUserChecks.filter(x => {
      let mn = x.dateTime.getMonth();
      return mn == month
    });

    this.loading = false;
    if (this.checks.length == 0)
      this.haveNoResults = true;
  }

  monthOnChange() {
    if (!this.selectedMonthId) {
      this.showDefaultResult();
      return;
    }

    this.sortResults(this.selectedMonthId);
  }

  onChangeDropdownIdentity(event: any) {
    if (!this.identityDropdownModel) {
      setTimeout(() => {
        this.identityDropdownModel = this.user.id;
        this.getChecksByIdentity(this.user.id);
      }, 100);
      return;
    }
    this.getChecksByIdentity(this.identityDropdownModel);
  }

  getChecksByIdentity(identityId) {
    this.loading = true;
    this.apiService.getChecks(identityId).subscribe(response => {
      this.mapGetCheckResponseToCheck(response);
      this.monthOnChange();
      this.currentIdentitiy = this.identities.find(x => { return x.id == identityId });
      this.loading = false;
    })
  }

  onChooseResult(event: any) {
    this.cardLoading = true;
    let cardResult = $(`#${event.id}`);

    if (this.cardIsOpened(event.id)) {
      this.closeCardById(event.id);
      return;
    }
    this.openCardById(event.id);
  }

  downloadQRCode(event: any) {
    if (!this.cardIsOpened(event.id)) {
      this.openCardById(event.id);
      return;
    }

    let currentCheck = this.checks.find(x => x.id == event.id);

    let qrCodeSrc = '';

    try {
      let qrCodeHtmlElement = document.getElementById(`qrcode-${event.id}`).childNodes[0];
      let child = qrCodeHtmlElement.childNodes[0];
      qrCodeSrc = (child as any).src;
    }
    catch (exception) {
      this.toastr.errorToastr('Error occurred when trying to generate HealthTest result!', 'Oops!', { showCloseButton: true });
      return;
    }
  }

  private collapseAllCards() {
    var btnsCardResult = document.querySelectorAll('.card-result__btn-js');
    btnsCardResult.forEach(function (btn) {
      btn.parentElement.classList.remove('card-result--large');
      btn.parentElement.style.removeProperty('left');

      btn.parentElement.classList.add('card-result--small')
    });
  }

  private openCardById(id: any) {
    this.cardLoading = true;
    this.collapseAllCards();
    let cardResult = $(`#${id}`);

    if (!cardResult)
      return;

    cardResult.removeClass('card-result--small');
    cardResult.addClass("card-result--large");

    let currentCheck = this.checks.find(x => x.id == id);

    this.cardLoading = false;
  }

  private closeCardById(id: any) {
    let cardResult = $(`#${id}`);
    cardResult.removeClass('card-result--large');
    cardResult.addClass('card-result--small');

    var btnsCardResult = document.querySelectorAll('.card-result__btn-js');
    btnsCardResult.forEach(function (btn) {
      btn.parentElement.style.removeProperty('left');
    });
  }

  private cardIsOpened(id: any): boolean {
    let cardResult = $(`#${id}`);
    if (!cardResult)
      return false;

    return cardResult.hasClass('card-result--large');
  }

  private mapGetCheckResponseToCheck(getChecksResponse: GetChecksResppnseModel) {
    this.allUserChecks = getChecksResponse.items;
    this.allUserChecks.sort((x1, x2) => x2.timestamp - x1.timestamp);
    this.allUserChecks.forEach(x => {
      x.dateTime = new Date(x.timestamp);
      x.dateTimeLocaleString = new Date(x.timestamp).toLocaleString("en-US").toString()
    })

    this.sortResults(null);
  }

}
