import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ScriptLoaderService } from '../../_services/script-loader.service';
import { log } from 'util';
import { User } from 'src/app/_models/user/user';
import { LocalStorageService } from 'src/app/_services/localStorage.service';

declare let mLayout: any;
@Component({
  selector: 'header-standart',
  templateUrl: './header-standart.component.html',
  styleUrls: ['./header-standart.component.scss']
})
export class HeaderStandartComponent implements OnInit {

  public user: User;

  constructor(private _router: Router, private localStorageService: LocalStorageService) {

  }

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem('currentUser'));
  }

  logout() {
    localStorage.removeItem("currentUser");
    this._router.navigate(['/login']);
  }
}
