import { PopowerErrorViewModel } from "../popowerErrorViewModel";
import { BackendResponseCode } from '../BackendResponseCodes'
import { PopowerErrorMessages } from "../../_models/AppConstants";
import { ValidationService } from "../../_services/helpers/validationService";
import { Gender } from "../user/user";




export interface SignUpFormModel {
    email: string;
    password: string;
    phone: string;
    firstName: string;
    lastName: string;
    gender: string;
    dateOfBirth: string;
    country: string;
    facilityCode: string;
}

export class UserSignUpModel {
    email: string;
    password: string;
    firstName: string;
    lastName: string;
    gender: any = Gender.PREFERE_NOT_TO_SHARE;
    phone: string;
    facilityCode: string;
    countryId: string;
    stateId: string;
    eula: any;
    dateOfBirth: any;
    dateOfBirthForView: any;
    checkTerms: any;

    public parseDateOfBirth() {
        let parsedDate = new Date(this.dateOfBirthForView);
        this.dateOfBirth = new Date(Date.UTC(parsedDate.getFullYear(), parsedDate.getMonth(), parsedDate.getDate())).toUTCString();
    }

}

export class SignUpErrorViewModel {

    public errorOccurred: boolean;

    public emailErrorPopover: PopowerErrorViewModel = new PopowerErrorViewModel()
    public passwordErrorPopover: PopowerErrorViewModel = new PopowerErrorViewModel()
    public facilityCodeErrorPopover: PopowerErrorViewModel = new PopowerErrorViewModel()

    public emailError = false;
    public passwordError = false;
    public phoneError: boolean;
    public firstNameError: boolean;
    public lastNameError: boolean;
    public sexError: boolean;
    public dateOfBirthError: boolean;
    public countryError: boolean;
    public stateError: boolean;
    public facilityCodeError: boolean;
    public termsAndConditionError: boolean;

    public setStateNoErrors() {
        this.errorOccurred = false;

        this.emailErrorPopover.hidePopower();
        this.passwordErrorPopover.hidePopower();
        this.facilityCodeErrorPopover.hidePopower();

        this.emailError = false;
        this.passwordError = false;
        this.phoneError = false;
        this.firstNameError = false;
        this.lastNameError = false;
        this.sexError = false;
        this.dateOfBirthError = false;
        this.countryError = false;
        this.stateError = false;
        this.facilityCodeError = false;
        this.termsAndConditionError = false;

    }

    public setStateErrorByResponseCode(code: BackendResponseCode) {
        if (!code)
            return;
        switch (code) {
            case BackendResponseCode.WRONG_FACILITY_CODE: {
                this.errorOccurred = true;
                this.facilityCodeError = true;
                this.facilityCodeErrorPopover.showPopower(PopowerErrorMessages.ENTER_THE_VALID_FACILITy_CODE);
                break;
            }
            case BackendResponseCode.CREATE_USER_ERROR: {
                this.errorOccurred = true;
                this.emailError = true;
                this.emailErrorPopover.showPopower(PopowerErrorMessages.EMAIL_ALREADY_TAKEN);
                break;
            }
        }
    }
}
