import { BackendResponseCode } from '../BackendResponseCodes'
import { ValidationService } from "../../_services/helpers/validationService";
import { AppConstants, PopowerErrorMessages } from "../../_models/AppConstants";
import { PopowerErrorViewModel } from "../popowerErrorViewModel";

export class UserRestorePasswordModel {
    email: string;
}

export class RestorePasswordErrorViewModel {

    public errorOccurred: boolean;

    public emailErrorPopover: PopowerErrorViewModel = new PopowerErrorViewModel()
    public emailError: boolean;

    public setStateNoErrors() {
        this.errorOccurred = false;
        this.emailErrorPopover.hidePopower();
        this.emailError = false;
    }

    public setStateErrorByResponseCode(code: BackendResponseCode) {
        if (!code)
            return;
        switch (code) {
            case BackendResponseCode.USER_NOT_FOUND: {
                this.errorOccurred = true;
                this.emailError = true;
                this.emailErrorPopover.showPopower(PopowerErrorMessages.USER_NOT_FOUND);
                break;
            }
            case BackendResponseCode.USER_NOT_ACTIVE: {
                this.errorOccurred = true;
                this.emailError = true;
                this.emailErrorPopover.showPopower(PopowerErrorMessages.USER_NOT_ACTIVE);
                break;
            }
        }
    }

    public checkEmailInput(email: string) {
        this.setStateNoErrors();
        if (!email) {
            this.errorOccurred = true;
            this.emailError = true;
            return;
        }
        if (!ValidationService.ifEmailMatchPattern(email)) {

            this.errorOccurred = true;
            this.emailError = true;
            this.emailErrorPopover.showPopower(PopowerErrorMessages.EMAIL_PATTERN_NOT_MATCHES);
            return;
        }
    }
}