import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MyProfileComponent } from './my-profile/my-profile.component';
import { MyResultComponent } from './my-results/my-result.component';
import { ProfileComponent } from './profile.component';
import { AuthGuard } from '../_services/guard.service';
import { Routes, RouterModule } from '@angular/router';
import {NgbDropdownModule, NgbTabsetModule, NgbTooltipModule} from '@ng-bootstrap/ng-bootstrap';
import { NgSelectModule } from '@ng-select/ng-select';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { QRCodeModule } from "angularx-qrcode";
import { HashLocationStrategy, LocationStrategy } from '@angular/common';
import {DropdownComponent} from '../layout/dropdown/dropdown.component';
import {DataSharingService} from '../_services/data-sharing.service';
import { ChildCardComponent } from './children/child-card-component/child-card.component';
import { ChildrenComponent } from './children/children.component';

const routes: Routes = [
  {
    "path": "profile",
    "component": ProfileComponent,
    "children": [
      {
        "path": "my-profile",
        "component": MyProfileComponent
      },
      {
        "path": "children",
        "component": ChildrenComponent
      },
      {
        "path": "my-result",
        "component": MyResultComponent
      },
    ]
  }
];

@NgModule({
  declarations: [
    MyProfileComponent,
    MyResultComponent,
    ProfileComponent,
    ChildCardComponent,
    ChildrenComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgbDropdownModule,
		NgbTabsetModule,
    NgbTooltipModule,
    NgbModule,
    FormsModule,
    NgSelectModule,
    QRCodeModule,
  ],
  exports: [
    RouterModule,
  ],
providers: [
  {provide: LocationStrategy, useClass: HashLocationStrategy},
  DataSharingService
]})
export class ProfileModule { }
