import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { HealthCheckResult } from '../_models/AppConstants';
import { QRCodeService } from '../_services/qrCode.service';
import { ToastrManager } from 'ng6-toastr-notifications';
import { Check, CheckModelDTO } from '../_models/health-test/HealthCheckModel';

@Component({
  selector: 'app-health-check-result',
  templateUrl: './health-check-result.component.html'
})
export class HealthCheckResultComponent implements OnInit {
  public check: Check;

  constructor(private _router: Router, private toastr: ToastrManager) { }

  ngOnInit() {
    let responseModel = JSON.parse(localStorage.getItem('checkModel'));
    localStorage.removeItem('checkModel');

    if (!responseModel) {
      this._router.navigate(['/health-test']);
      return;
    }
    this.parseResponseModel(responseModel);
  }

  private parseResponseModel(checkResponseModel: CheckModelDTO) {
    this.check = checkResponseModel as Check;

    this.check.dateTime = new Date(this.check.timestamp).toLocaleString("en-US");
  }

  downloadQRCode() {

    let qrCodeSrc = '';

    try {
      let qrCodeHtmlElement = document.querySelector('.qrcode');
      let child = qrCodeHtmlElement.childNodes[0];
      qrCodeSrc = (child as any).src;
    }
    catch (exception) {
      this.toastr.errorToastr('Error occurred when trying to generate HealthTest result!', 'Oops!', { showCloseButton: true });
      return;
    }

  }
}