@Injectable()
export class DataSharingService {
  public selectedFacility: BehaviorSubject<string> = new BehaviorSubject<string>('');
  public test: BehaviorSubject<string> = new BehaviorSubject<string>(null);
    
  public childAdded: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
}