import { Injectable, ComponentFactoryResolver } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor, HttpErrorResponse, HttpResponse, HttpClient } from '@angular/common/http';
import { Observable, Subject, BehaviorSubject, throwError, } from 'rxjs';
import { Router } from '@angular/router';
import { tap, catchError, mapTo, switchMap, filter, take } from 'rxjs/operators';
import { ToastrManager } from 'ng6-toastr-notifications';
import { User, UserLoginModel } from 'src/app/_models/user/user';
import { AuthenticationService } from '../auth.service';
import { LocalStorageService } from '../localStorage.service';


@Injectable()
export class AppInterceptor implements HttpInterceptor {

  private isRefreshing = false;
  private readonly TOKEN_EXPIRED = 'Token-Expired';
  private refreshTokenSubject: BehaviorSubject<any> = new BehaviorSubject<any>(null);


  constructor(private toastr: ToastrManager, private _router: Router, private http: HttpClient,
    private authService: AuthenticationService, private localStorageService: LocalStorageService) {
  }


  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    let currentUser = JSON.parse(localStorage.getItem("currentUser"));

    if (currentUser && currentUser.token) {
      request = this.addToken(request, currentUser.token);
    }

    return next.handle(request).pipe(catchError(error => {
      //const isTokenExpired = error.headers.get(this.TOKEN_EXPIRED);
      if (error instanceof HttpErrorResponse && error.status === 401) {
        return this.handle401Error(request, next, currentUser)
      }

      this.showToastrError(request, error);
      return next.handle(request);
    }
    ));
  }


  private handle401Error(request: HttpRequest<any>, next: HttpHandler, currentUser: User) {
    if (!this.isRefreshing) {
      console.log("token refreshing");
      this.isRefreshing = true;
      this.refreshTokenSubject.next(null);

      return this.refreshToken(currentUser).pipe(
        switchMap((token: any) => {
          this.isRefreshing = false;
          this.refreshTokenSubject.next(token.accessToken);
          return next.handle(this.addToken(request, token.accessToken));
        }));

    } else {
      return this.refreshTokenSubject.pipe(
        filter(token => token != null),
        take(1),
        switchMap(jwt => {
          return next.handle(this.addToken(request, jwt));
        }));
    }
  }

  private refreshToken(currentUser: User) {
    let params = {
      accessToken: currentUser.token,
      refreshToken: currentUser.refreshToken
    };

    return this.authService.refreshToken(params).pipe(tap((response: any) => {
      this.updateUserToken(currentUser, response.accessToken, response.refreshToken);
    }), catchError(error => {
      this.isRefreshing = false;
      localStorage.removeItem("currentUser");
      this._router.navigate(['/login']);
      return throwError(error);
    }));
  }

  private updateUserToken(currentUser: User, accessToken, refreshToken) {
    currentUser.token = accessToken;
    currentUser.refreshToken = refreshToken;
    this.localStorageService.updateUserTokens(accessToken, refreshToken);
  }

  private addToken(request: HttpRequest<any>, token: string) {
    const authHeader = `Bearer ${token}`;
    return request = request.clone({
      headers: request.headers.set('Authorization', authHeader)
    });
  }

  private showToastrError(request: any, error: any) {

    if (error.status === 0) {
      this.showErrorToastr(request, error);
      return;
    }

    if (!error.status || !error.message)
      return;

    if (error.status === 500) {
      this.handle500(request, error);
      return;
    }

    if (error.status === 404) {
      //this.showErrorToastr(request, error);
      return;
    }

    if (error.status === 401) {
      localStorage.removeItem("currentUser");
      this._router.navigate(['/login']);
      return;
    }

  }

  private handle500(request, error: any) {
    let errorMessage = `Error occurred for request ${request.url} <br>StatusCode: ${error.status}<br><br>${error.message}`;

    if (error.error) {
      if (error.error.Message)
        errorMessage += `<hr style="border-top: 1px solid rgba(225,225,225);"><b>Details:</b><br>${error.error.Message}`;
      if (error.error.StackTrace)
        console.error(error.error.StackTrace);
    }
    this.toastr.errorToastr(errorMessage, 'Oops!', { enableHTML: true, dismiss: "click", showCloseButton: true });
  }

  private showErrorToastr(request, error: any) {
    let errorMessage = `Error occurred for request ${request.url} <br>StatusCode: ${error.status}<br><br>${error.message}`;
    this.toastr.errorToastr(errorMessage, 'Oops!', { enableHTML: true, dismiss: "click", showCloseButton: true });
  }
}
