import { from } from "rxjs";

export class DataProviderExtension {

    public static monthNamesEN: any = ["January", "February", "March", "April", "May", "June",
        "July", "August", "September", "October", "November", "December"
    ];

    public static monthNamesUK: any = ["c", "", "", "", "", "", "",
        "", "", "", "", ""];

    public static dateTimeToUkrainianString(miliseconds: number) {
        let d = new Date(miliseconds * 1000);
        let cDay = d.getDate();
        let cYear = d.getFullYear();
        let cMonth = this.monthNamesUK[d.getMonth()];

        return `${cDay} ${cMonth} ${cYear}`
    }

    public static splitSequenceToRows(source: Array<any>, eltInOneRow: number): Array<Array<any>> {
        debugger;
        const x = source.length / eltInOneRow

        let rowsCount = Math.trunc(x); // returns 3
        let float_part = Number((x - rowsCount).toFixed(2)); // return 0.2

        if (float_part > 0)
            rowsCount++

        let result = [];

        for (let index = 0; index < rowsCount; index++) {
            let part = source.slice(index*eltInOneRow, eltInOneRow);
            result.push(part);
        }
        return result;
    }
} 