import { Component, OnInit, Input } from '@angular/core';
import { User } from 'src/app/_models/user/user';
import { AddFacilityViewModel, FacilityResponseModel } from 'src/app/_models/profile/addFacilityViewModel';
import { ApiService } from 'src/app/_services/api/api.service';
import { Router } from '@angular/router';
import { LocalStorageService } from 'src/app/_services/localStorage.service';
import { Child } from 'src/app/_models/children/child';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { QRCodeService } from 'src/app/_services/qrCode.service';


@Component({
    selector: 'child-card',
    templateUrl: './child-card.component.html',
    styleUrls: ['child-card.component.scss']
})

export class ChildCardComponent implements OnInit {

    @Input() childId: any;

    public child: Child = new Child;
    public childIdBase64: string;
    public addFacilityDialogViewModel = new AddFacilityViewModel();
    public loading: boolean = false;
    public qrCodeModel: any;

    constructor(private apiService: ApiService,
        private router: Router,
        private localStorageService: LocalStorageService,
        private modalService: NgbModal) {
    }

    ngOnInit(): void {
        this.loading = true;
        let children = this.localStorageService.getChildren() as Array<Child>;

        this.child = children.find((item) => { return item.id === this.childId });
        this.loading = false;
        if (!this.child) {
            this.child = new Child();
            console.error("ChildById not found");
            console.error(`Id: ${this.childId}`);
            this.router.navigate(['/profile/my-profile']);
            return;
        }
        this.childIdBase64 = btoa(this.child.id);
    }

    routeEditChild() {
        let childId = btoa(this.child.id);

        this.router.navigate([
            `edit-child-profile/${childId}`]);
    }

    onClickAddFacilityInput() {
        if (!this.addFacilityDialogViewModel.newFacilityCode || this.addFacilityDialogViewModel.facilityErrorPopover.popowerIsVisible)
            return;
        this.addFacilityDialogViewModel.loading = true;

        let getFacilityResponseBody = { facilityCode: this.addFacilityDialogViewModel.newFacilityCode };

        this.apiService.getFacilityCode(getFacilityResponseBody).subscribe((response: FacilityResponseModel) => {

            if (!response.success) {
                this.addFacilityDialogViewModel.handleResponse(response);
                this.addFacilityDialogViewModel.loading = false;
                return;
            }

            let body = { userId: this.child.id, facilityCode: this.addFacilityDialogViewModel.newFacilityCode };
            this.apiService.addFacility(body).subscribe((response: any) => {
                this.child.facilities = response;
                this.addFacilityDialogViewModel.loading = false;
                this.addFacilityDialogViewModel.setAddFacilityDialogIsActive(false);
            });
        });
    }

    openModalShareProfile(shareprofilemodal) {
        
    }

    copyChildId() {
        try {
            let selBox = document.createElement('textarea');
            selBox.style.position = 'fixed';
            selBox.style.left = '0';
            selBox.style.top = '0';
            selBox.style.opacity = '0';
            selBox.value = this.childIdBase64;
            document.body.appendChild(selBox);
            selBox.focus();
            selBox.select();
            document.execCommand('copy');
            document.body.removeChild(selBox);

            $("#copyChildIdBtn").animate({ opacity: 0 }, 150, () => {
                $("#copyChildIdBtn span").text("Child Id copied to clipboard");
                $("#copyChildIdBtn").animate({ opacity: 100 }, 150);
            });
        }
        catch (ex) {
            $("#copyChildIdBtn").animate({ opacity: 0 }, 150, () => {
                $("#copyChildIdBtn").text("Oops! Error occurred.");
                $("#copyChildIdBtn").css({color: '#c43737'});
                $("#copyChildIdBtn").animate({ opacity: 100 }, 150);
            });
        }
    }
}