import { Component, OnInit } from '@angular/core';
import { ApiService } from '../_services/api/api.service';
import { AppConstants, PopowerErrorMessages, InputPatterns } from '../_models/AppConstants'
import { ToastrManager } from 'ng6-toastr-notifications';
import { EditProfileErrorViewModel, EditUserViewModel } from '../_models/profile/profileModels'
import { ValidationService } from "../_services/helpers/validationService";
import { Router } from '@angular/router';
import { User, Country, State } from '../_models/user/user';
import { LocalStorageService } from '../_services/localStorage.service';
import { ContentHelper } from '../_services/helpers/contentHelper';

declare let $: any;
@Component({
  selector: 'edit-profile',
  templateUrl: './edit-profile.component.html',
})

export class EditProfileComponent implements OnInit {

  public loading: boolean = false;
  public emailPattern = InputPatterns.EMAIL_PATTERN;
  public user: User;
  public editUserModel: EditUserViewModel = new EditUserViewModel();
  public editProfileErrorViewModel: EditProfileErrorViewModel = new EditProfileErrorViewModel();
  public countries: Array<Country>;
  public states: Array<State>;
  public country_USA: any;
  public USACountrySelected = false;

  constructor(private apiService: ApiService,
    private toastr: ToastrManager,
    private router: Router,
    private localStorageService: LocalStorageService) {

  }

  ngOnInit(): void {
    ContentHelper.setLoading(true);
    this.loading = true;

    this.user = this.localStorageService.getUser();
    if (!this.user) {
      this.router.navigate(['/login']);
      return;
    }

    this.countries = JSON.parse(localStorage.getItem('countries')) as Array<Country>;
    this.states = JSON.parse(localStorage.getItem('states')) as Array<State>;

    if (!this.countries || !this.states) {
      this.apiService.getCountries().subscribe((response: any) => {
        response.items.forEach(c => {
          if (c.alpha2Code === AppConstants.USA_COUNTRY_ALPHA2CODE) {
            c.name = "United States";
          }
        });
        
        this.countries = response.items;
        localStorage.setItem(AppConstants.LOCAL_STORAGE_COUNTRIES_KEY, JSON.stringify(response.items));
        this.country_USA = this.countries.find(x => x.alpha2Code == AppConstants.USA_COUNTRY_ALPHA2CODE);

        this.apiService.getStates().subscribe((response: any) => {
          this.states = response.items;
          localStorage.setItem(AppConstants.LOCAL_STORAGE_STATES_KEY, JSON.stringify(response.items));

          this.initEditProfileModels();
        });
      });
    } else {
      this.country_USA = this.countries.find(x => x.alpha2Code == AppConstants.USA_COUNTRY_ALPHA2CODE);
      this.initEditProfileModels();
    }

    var date_input = $('#dateOfBirth-new'); //our date input has the name "date"
    date_input.datepicker({
      orientation: "bottom auto"
    });

    if (this.editUserModel.dateOfBirthViewModel) {
      let userDateOfBirth = new Date(this.editUserModel.dateOfBirthViewModel);
      $("#dateOfBirth-new").datepicker("setDate", userDateOfBirth);
    }

    date_input.change((event) => {
      this.editUserModel.dateOfBirthViewModel = date_input.val();
      this.formChangedBaseHandler()
    });

  }

  initEditProfileModels() {

    this.editUserModel.mapFrom(this.user);
    this.onCountrySelected(this.editUserModel.countryId);
    this.editUserModel.stateId = this.user.state ? this.user.state.id : undefined;
    this.checkFormFields();

    ContentHelper.setLoading(false);
    this.loading = false;
  }

  formChangedBaseHandler() {
    this.checkFormFields();
  }

  emailChangeValue(event: any) {
    this.checkFormFields();
    if (this.editUserModel.email && !ValidationService.ifEmailMatchPattern(this.editUserModel.email)) {
      this.editProfileErrorViewModel.emailErrorPopover.showPopower(PopowerErrorMessages.EMAIL_PATTERN_NOT_MATCHES);
      this.editProfileErrorViewModel.emailError = true;
    }
  }

  // Show fields with red color to show that field should be filled
  checkFormFields() {
    this.editProfileErrorViewModel.setStateNoErrors();

    if (!this.editUserModel.email || !ValidationService.ifEmailMatchPattern(this.editUserModel.email))
      this.editProfileErrorViewModel.emailError = true;

    if (!this.editUserModel.phone)
      this.editProfileErrorViewModel.phoneError = true;

    if (!this.editUserModel.firstName || !ValidationService.ifNameMatchPattern(this.editUserModel.firstName))
      this.editProfileErrorViewModel.firstNameError = true;

    if (!this.editUserModel.lastName || !ValidationService.ifNameMatchPattern(this.editUserModel.lastName))
      this.editProfileErrorViewModel.lastNameError = true;

    if (this.editUserModel.gender == undefined)
      this.editProfileErrorViewModel.sexError = true;

    if (!this.editUserModel.dateOfBirthViewModel)
      this.editProfileErrorViewModel.dateOfBirthError = true;

    if (!this.editUserModel.countryId)
      this.editProfileErrorViewModel.countryError = true;

    if (!this.editUserModel.stateId)
      this.editProfileErrorViewModel.stateError = true;
  }

  onCountrySelected(value: string) {
    this.USACountrySelected = value === this.country_USA.id;
    this.editUserModel.stateId = undefined;
  }

  saveChanges() {
    this.editUserModel.dateOfBirthToUTC();
    this.loading = true;

    this.apiService.updateProfile(this.user.id, this.editUserModel).subscribe((response: any) => {
      this.loading = false;
      if (response.success) {
        this.toastr.successToastr("Profile updated successfully", 'Success!');
        this.mapUserFromUpdateResponse(response);
        this.router.navigate(['/profile/my-profile']);
      } else {
        this.toastr.errorToastr(response.errors.join(','), 'Error');
      }
    }, error => {
      this.toastr.errorToastr('Error occurred', 'Oops!');
      this.loading = false;
    })
  }

  mapUserFromUpdateResponse(response: any) {
    let actualUserModel = JSON.parse(localStorage.getItem('currentUser'));

    actualUserModel.firstName = response.firstName;
    actualUserModel.lastName = response.lastName;
    actualUserModel.age = response.age;
    actualUserModel.phone = response.phone;
    actualUserModel.country = response.country
    actualUserModel.state = response.state
    actualUserModel.temperatureScale = response.temperatureScale
    actualUserModel.dateOfBirth = response.dateOfBirth
    actualUserModel.gender = response.gender;
    localStorage.setItem(AppConstants.LOCAL_STORAGE_USER_KEY, JSON.stringify(actualUserModel));
    this.user = actualUserModel;
  }

}
