import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import {UserLoginModel, User} from '../../_models/user/user'
import { UserSignUpModel } from '../../_models/sign-up/signUpModel'
import { UserRestorePasswordModel } from '../../_models/restorePassword/restorePasswordModels'
import { EditUserViewModel } from '../../_models/profile/profileModels'
import { AppConstants } from "../../_models/AppConstants"
import { environment } from "src/environments/environment";
import { Observable } from "rxjs";
import { GetUserByIdResponseModel } from "src/app/_models/api/responseModel/getUserByIdResponseModel";
import { EditProfileRequestModel } from "src/app/_models/api/requestModels/editProfileRequestModel";
import { GetChildrenResponseModel } from "src/app/_models/api/responseModel/getChildrenResponseModel";
import { CreateChildRequestModel } from "src/app/_models/api/requestModels/createChildRequestModel";
import { CreateChildResponseModel } from "src/app/_models/api/responseModel/createChildResponseModel";
import { GetChecksResppnseModel } from "src/app/_models/api/responseModel/getChecksResponseModel";
import { CheckResponseModel } from "src/app/_models/api/responseModel/checkResponseModel";


@Injectable()
export class ApiService {

    constructor(private http: HttpClient) {
    }

    getCountries() : Observable<any>{
        return this.http.get(environment.apiUrl + '/api/Countries');
    }

    getStates() : Observable<any>{
        return this.http.get(environment.apiUrl + '/api/States');
    }

    getUser(id: string) : Observable<GetUserByIdResponseModel> {
        return this.http.get<GetUserByIdResponseModel>(`${environment.apiUrl}/api/user/${id}`);
    }

    login(loginModel: UserLoginModel) {

        let body = JSON.stringify({ userName: loginModel.email, password: loginModel.password });
        return this.http.post(environment.apiUrl + '/api/auth/login', {userName: loginModel.email, password: loginModel.password});
    }

    signUp(usignUpModel: UserSignUpModel){
        return this.http.post(environment.apiUrl + '/api/Auth/signin', usignUpModel);
    }   

    restorePassword(forgotpasswordModel: UserRestorePasswordModel) {
        return this.http.post(environment.apiUrl + "/api/auth/forgot-password", forgotpasswordModel);
    }

    checkTest(body: any) : Observable<CheckResponseModel> {
        return this.http.post<CheckResponseModel>(environment.apiUrl + '/api/checks', body);
    }

    updateProfile(userId: string, updateModel: EditUserViewModel) {

        return this.http.put(environment.apiUrl + '/api/User/' + userId, updateModel);
    }

    updateChildProfile(childId: string, updateModel: EditProfileRequestModel) {
        return this.http.put(environment.apiUrl + '/api/User/' + childId, updateModel);
    }

    getChecks(userId: string): Observable<GetChecksResppnseModel>{
        return this.http.get<GetChecksResppnseModel>(environment.apiUrl + `/api/User/${userId}/checks`);
    }

    addFacility(body: any) {
        return this.http.post(environment.apiUrl + '/api/User/add-facility-user', body);
    }

    getFacilityCode(body: any) {
        return this.http.post(environment.apiUrl + '/api/Check/GetFacilityByCode', body);
    }

    updatePassword(body: any) {
        return this.http.post(environment.apiUrl + '/api/User/update-user-password', body);
    }

    getChildren(): Observable<GetChildrenResponseModel>{
        return this.http.get<GetChildrenResponseModel>(environment.apiUrl + `/api/Children`);
    }

    postChild(body: CreateChildRequestModel) : Observable<CreateChildResponseModel>{
        return this.http.post<CreateChildResponseModel>(environment.apiUrl + `/api/Children`, body);
    }

    postAddChildByCode(id: string) : Observable<any>{
        return this.http.post<any>(`${environment.apiUrl}/api/Children/${id}`, {});
    }

    leaveFacility(userId: string, facilityId: string): Observable<any>{
        let params: any = {};
        params.userId = userId;
        params.facilityId = facilityId;
        return this.http.post<any>(`${environment.apiUrl}/api/User/leave-facility`, params);
    }

}