import { Injectable } from "@angular/core";
import { AppConstants } from "../_models/AppConstants";

@Injectable()
export class LanguageService {

    public getCurrentLanguage(): string {
        if (navigator)
            return this.parseLanguageCode(navigator.language);
        else
            AppConstants.EN_LANGUAGE_CODE
    }

    private parseLanguageCode(code: string) {
        let result;
        try {
            result = code.split('-')[0];
        }
        catch (ex) {
            result = AppConstants.EN_LANGUAGE_CODE;
        }

        if (!result) {
            return AppConstants.EN_LANGUAGE_CODE;
        }

        return result;
    }

}

