import { AnswerRequestModel, CheckRequestModel } from "src/app/_models/api/requestModels/checkRequestModel";
import { AppConstants } from "src/app/_models/AppConstants";
import { ProgressBarViewModel, Question } from "./CustomSurveyModels";

export class CustomSurveyWizard {

    private EN_LANGUAGE_CODE: string = AppConstants.EN_LANGUAGE_CODE;

    private baseSurveyModel: Array<Question> = [];
    private currentSurvey: Array<Question> = [];

    private sequenceStepper: SequenceStepper<Question>;
    private progressBarModel: ProgressBarModel;
    private surveyAnswersService: SurveyAnswersService;

    constructor(questions: any, locale: string) {
        this.baseSurveyModel = questions;
        this.currentSurvey = this.parseSurvey(this.baseSurveyModel, locale);
        this.sequenceStepper = new SequenceStepper(this.currentSurvey);
        this.progressBarModel = new ProgressBarModel(this.currentSurvey.length);
        this.surveyAnswersService = new SurveyAnswersService();
    }

    private parseSurvey(baseSurvey: Array<Question>, locale: string): Array<Question> {
        let localizedQuestions = baseSurvey.filter(x => { return x.languageCode === locale });

        if (!localizedQuestions || localizedQuestions.length === 0)
            localizedQuestions = baseSurvey.filter(x => { return x.languageCode === this.EN_LANGUAGE_CODE });

        if (!localizedQuestions || localizedQuestions.length === 0) {
            let locale = baseSurvey[0].languageCode;
            localizedQuestions = baseSurvey.filter(x => { return x.languageCode === locale });
        }

        if (!localizedQuestions || localizedQuestions.length === 0)
            throw "Survey not valid";

        let groupedQuestions = localizedQuestions.sort((x, y) => {
            return x.order > y.order ? 1 : -1;
        });

        return groupedQuestions;
    }

    nowIsFirstQuestion(): boolean {
        return this.sequenceStepper.isRichedFirstItem;
    }

    getProgressBarViewModel(): ProgressBarViewModel {
        return this.progressBarModel.getProgressViewModel();
    }

    startWizard() {
        this.progressBarModel.start()
        this.sequenceStepper.start();
        this.surveyAnswersService.start();

        this.sequenceStepper.moveForward();
    }

    handleAnswer(answer: any) {
        this.surveyAnswersService.handleAnswer(this.sequenceStepper.currentItem, answer, this.sequenceStepper.index);
    }

    nextQuestion(): Question {
        this.sequenceStepper.moveForward();
        this.progressBarModel.moveForward();
        return this.sequenceStepper.currentItem;
    }

    getCurrentQuestion(): Question {
        return this.sequenceStepper.currentItem;
    }

    previousQuestion(): Question {
        this.sequenceStepper.moveBack();
        this.progressBarModel.moveBack();
        return this.sequenceStepper.currentItem;
    }

    ifFinished(): boolean {
        return this.sequenceStepper.isRichedLastItem
    }

    getSurveyAnswers(userId: string, facilityId: string, surveyId: string) : CheckRequestModel{
        return SurveyResultFactory.GetResult(userId, facilityId, surveyId, this.surveyAnswersService.answers);
    }
}

export class SequenceStepper<T> {

    private currentSequence: Array<T> = [];

    private currentIndex = -1;
    private _isRichedLastItem = false;

    get isRichedFirstItem(): boolean {
        return this.currentIndex === 0;
    }

    get isRichedLastItem(): boolean {
        return this._isRichedLastItem;
    }

    get index(): number {
        return this.currentIndex;
    }

    get currentItem(): T {
        return this.currentSequence[this.currentIndex];
    }

    constructor(sequence: Array<T>) {
        this.currentSequence = sequence;
    }

    public start() {
        this.currentIndex = -1;
        this._isRichedLastItem = false;
    }

    public moveForward(): T {
        this.currentIndex++;

        if (this.currentIndex === this.currentSequence.length) {
            this.currentIndex = this.currentSequence.length - 1;
            this._isRichedLastItem = true;
        }

        return this.currentItem;
    }

    public moveBack(): T {
        this.currentIndex--;

        if (this.currentIndex < 0)
            this.currentIndex = 0

        return this.currentItem;
    }

}

export class ProgressBarModel {
    private currentQuestionNumber: number = 1;

    constructor(private questionsCount: number) {
    }

    start() {
        this.currentQuestionNumber = 1;
    }

    moveForward() {
        this.currentQuestionNumber++;
        if (this.currentQuestionNumber === this.questionsCount)
            this.currentQuestionNumber = this.questionsCount;
    }

    moveBack() {
        if (this.currentQuestionNumber === 1)
            return;
        this.currentQuestionNumber--;
    }

    private getProgress(): number {
        let p = (this.currentQuestionNumber / this.questionsCount) * 100;
        return p;
    }

    getProgressViewModel(): ProgressBarViewModel {
        let progressBarViewModel = new ProgressBarViewModel();
        progressBarViewModel.currentQuestion = this.currentQuestionNumber;
        progressBarViewModel.questionsCount = this.questionsCount;
        progressBarViewModel.progress = this.getProgress();
        return progressBarViewModel;
    }
}


export class SurveyAnswersService {

    private _answers: Array<AnswerModel> = [];

    get answers() {
        return this._answers;
    }

    start(){
        this._answers = new Array<AnswerModel>();
    }

    handleAnswer(question: any, answer: any, currentIndex: number) {
        let model = new AnswerModel(question, answer);
        this._answers[currentIndex] = model;
    }
}

export class SurveyResultFactory {

    public static GetResult(userId: string, facilityId: string, surveyId: string, answers: Array<AnswerModel>) : CheckRequestModel {
        let requestModel = new CheckRequestModel();
        requestModel.userId = userId;
        requestModel.facilityId = facilityId;
        requestModel.surveyId = surveyId;

        answers.forEach(item => {
            let answerRequestModel = new AnswerRequestModel();
            answerRequestModel.questionGroupId = item.question.questionGroupId
            answerRequestModel.value = item.answer;
            requestModel.answers.push(answerRequestModel);
        });

        return requestModel;
    }
}

export class AnswerModel {
    constructor(public question: Question, public answer: boolean) {
    }
}
