import { CreateChildRequestModel } from "src/app/_models/api/requestModels/createChildRequestModel";
import { EditProfileRequestModel } from "src/app/_models/api/requestModels/editProfileRequestModel";
import { GetChildrenResponseModel } from "src/app/_models/api/responseModel/getChildrenResponseModel";
import { GetUserByIdResponseModel } from "src/app/_models/api/responseModel/getUserByIdResponseModel";
import { Child, ChildBaseModel } from "src/app/_models/children/child";
import { CreateChildModel } from "src/app/_models/children/createChildModel";
import { EditChildModel } from "src/app/_models/children/editChildModel";

export class ChildMapper {


  public static mapCreateChildRequest(child: CreateChildModel): CreateChildRequestModel {
    let model = new CreateChildRequestModel;
    model.firstName = child.firstName;
    model.lastName = child.lastName;
    model.gender = child.gender;
    model.countryId = child.countryId;
    model.stateId = child.stateId;

    model.dateOfBirth = '';
    try {
      let numbers = child.dateOfBirth.split('/');
      let month = numbers[0];
      let date = numbers[1];
      let year = numbers[2];
      model.dateOfBirth = `${year}-${month}-${date}`;
    } catch (ex) { }

    return model;
  }

  public static mapEditChildRequest(child: EditChildModel): EditProfileRequestModel {
    let model = new EditProfileRequestModel;
    model.firstName = child.firstName;
    model.lastName = child.lastName;
    model.gender = child.gender;
    model.countryId = child.countryId;
    model.stateId = child.stateId;

    model.dateOfBirth = ChildMapper.parseUIStringToDateOfBirthRequestString(child.dateOfBirth);
    return model;
  }

  public static mapGetUserByIdResponseToChildModel(id: string, response: GetUserByIdResponseModel): Child {
    let child = new Child();
    child.id = id;
    child.firstName = response.firstName;
    child.lastName = response.lastName;
    child.gender = response.gender;
    child.country = response.country;
    child.state = response.state;
    child.facilities = response.facilities;

    child.dateOfBirth = response.dateOfBirth;
    return child;
  }

  public static mapChildToEditChildMoldel(child: Child): EditChildModel {
    let editModel = new EditChildModel();
    editModel.firstName = child.firstName;
    editModel.lastName = child.lastName;
    editModel.gender = child.gender;

    if (child.country && child.country.id)
      editModel.countryId = child.country.id;

    if (child.state && child.state.id)
      editModel.stateId = child.state.id;



    editModel.dateOfBirth = ChildMapper.parseDateOfBirthToUIString(child.dateOfBirth);
    return editModel;
  }

  private static parseUIStringToDateOfBirthRequestString(dateString: string): string {
    let result = '';
    try {
      let numbers = dateString.split('/');
      let month = numbers[0];
      let date = numbers[1];
      let year = numbers[2];
      if (month && date && year)
        result = `${year}-${month}-${date}`;
      else
        console.error(`DateOfBirth string wrong format: ${dateString}`);
    } catch (ex) { }

    return result;
  }

  //dateString format 2020-01-01T00:00:00+00:00 to 01/01/2020
  private static parseDateOfBirthToUIString(dateString: string): string {
    let result = '';
    try {
      let yearMonthDate = dateString.split('T')[0];
      if (!yearMonthDate)
        return "";

      let splitted = yearMonthDate.split('-');
      let year = splitted[0];
      let month = splitted[1];
      let date = splitted[2];

      if (!year || !month || !date)
        return "";

      result = `${month}/${date}/${year}`
    } catch (ex) { }
    return result;
  }
}