import { Question } from "src/app/_services/customQuestionWizard/CustomSurveyModels";

export class User {
  token: string;
  refreshToken: string;
  id: string;
  email: string;
  firstName: string;
  lastName: string;
  age: string;
  phone: string;
  userFacility: Array<Facility> = [];
  country: Country;
  state: State;
  availableTemperatureScales: Array<TemperatureScale> = [];
  temperatureScale: TemperatureScale;
  gender: Gender;
  dateOfBirth: any;
}

export enum Gender {
  PREFERE_NOT_TO_SHARE = 0,
  MALE = 1,
  FEMALE = 2,
  NON_BINARY = 3
}

export class Facility {
  facilityId: string;
  facilityName: string;
  
  alreadyAddedToUser: boolean;
  city: string;
  country: string;
  id: string;
  name: string;
  organizationId: string
  postCode: string;
  questions: Array<Question>
  regCode: string
  state: string
  streetAddress1: string
  streetAddress2: string
  surveyId: string
}

export class FacilityModelDTO {
  id: string;
  name: string;
  StreetAddress1: string;
  StreetAddress2: string;
  City: string;
  State: string
  Country: string;
  PostCode: string;
  RegCode: string;
}

export class Country {
  alpha2Code: string;
  id: string;
  name: string;
}

export class State {
  code: string;
  id: string;
  name: string;
}

export class LocalSettings {
  temperatureScale: string;
}

export class TemperatureScale {
  id: string;
  name: string;
}

export class UserLoginModel {
  email: string;
  password: string;
}

export class BaseErrorViewModel {
  public emailError: boolean;
  public passwordError: boolean;
}

export class SignInErrorViewModel extends BaseErrorViewModel {
  public userNotActiveError: boolean
  public userNotFountError: boolean

  public setStateNoError() {
    this.emailError = false;
    this.passwordError = false;
    this.userNotActiveError = false;
    this.userNotFountError = false;
  }
}

export class UpdateUserModel {
  userId: string;
  firstName: string;
  lastName: string;
  phone: string;
  age: string;
  countryId: string;
  stateId: string;
  temperatureScaleId: string;
  dateOfBirth: any;
}
