import { PopowerErrorViewModel } from "../popowerErrorViewModel";
import { BackendResponseCode } from '../BackendResponseCodes'
import { PopowerErrorMessages } from "../AppConstants";
import { User, TemperatureScale } from '../user/user';
import { NgbDate, NgbCalendar, NgbDateParserFormatter } from '@ng-bootstrap/ng-bootstrap';


export class EditProfileErrorViewModel {

    public errorOccurred: boolean;

    public emailErrorPopover: PopowerErrorViewModel = new PopowerErrorViewModel()

    public emailError = false;
    public phoneError: boolean;
    public firstNameError: boolean;
    public lastNameError: boolean;
    public sexError: boolean;
    public dateOfBirthError: boolean;
    public countryError: boolean;
    public stateError: boolean;

    public setStateNoErrors() {
        this.errorOccurred = false;

        this.emailErrorPopover.hidePopower();

        this.emailError = false;
        this.phoneError = false;
        this.firstNameError = false;
        this.lastNameError = false;
        this.sexError = false;
        this.dateOfBirthError = false;
        this.countryError = false;
        this.stateError = false;
    }

    public setStateErrorByResponseCode(code: BackendResponseCode) {
        if (!code)
            return;
        switch (code) {
            case BackendResponseCode.CREATE_USER_ERROR: {
                this.errorOccurred = true;
                this.emailError = true;
                this.emailErrorPopover.showPopower(PopowerErrorMessages.EMAIL_ALREADY_TAKEN);
                break;
            }
        }
    }
}

export class EditUserViewModel {
    email: string;
    firstName: string;
    lastName: string;
    gender: any;
    phone: string;
    countryId: string;
    stateId: string;
    temperatureScale: TemperatureScale;
    temperatureScaleId: string;
    dateOfBirth: string;
    dateOfBirthViewModel: any;

    public dateOfBirthToUTC() {
        try{
            
            if (this.dateOfBirthViewModel) {
                let dt = new Date(this.dateOfBirthViewModel);
                this.dateOfBirth = new Date(Date.UTC(dt.getFullYear(), dt.getMonth(), dt.getDate())).toUTCString();
            }
            else {
                this.dateOfBirth = null;
            }
        }catch(error){
        }
    }

    public mapFrom(user: User) {
        if (!user) {
            user = new User;
        }

        this.email = user.email;
        this.firstName = user.firstName;
        this.lastName = user.lastName;
        this.gender = user.gender;
        this.phone = user.phone;

        this.countryId = user.country ? user.country.id : undefined;
        this.stateId = user.state ? user.state.id : undefined;

        this.temperatureScale = user.temperatureScale;
        this.temperatureScaleId = user.temperatureScale ? user.temperatureScale.id : null;
        try{
            let userDateOfBirth = new Date(user.dateOfBirth);
            this.dateOfBirthViewModel = userDateOfBirth;
        }catch (exeption){
            console.error(exeption)
            this.dateOfBirth = null;
        }
    }
}