export class ActivationRequestModel {
    userId: string;
    token: string;
    //isValid: boolean;

    isValid(): boolean {
        if (!this.userId || !this.token)
            return false;
        return true;
    }
}

export class ActivationViewModel {

    public activationInProgress: boolean;
    public requestPending: boolean
    public requestReceived: boolean;
    public activationSuccess: boolean;

    setStateActivationInProgress(): void{
        this.activationInProgress = true;
        this.requestPending = true;
        this.requestReceived = false;
        this.activationSuccess = false;
    }

    setStateRequestReceived(activationSuccess: boolean): void{
        this.activationInProgress = false;
        this.requestPending = false;
        this.requestReceived = true;
        this.activationSuccess = activationSuccess;
    }
}