import { ValidationService } from "src/app/_services/helpers/validationService";
import { PopowerErrorMessages } from "../AppConstants";
import { BackendResponseCode } from "../BackendResponseCodes";
import { PopowerErrorViewModel } from "../popowerErrorViewModel";

export class AddChildByCodeErrorViewModel {

    public errorOccurred: boolean;

    public codeErrorPopover: PopowerErrorViewModel = new PopowerErrorViewModel()
    public codeError: boolean;

    public setStateNoErrors() {
        this.errorOccurred = false;
        this.codeErrorPopover.hidePopower();
        this.codeError = false;
    }

    public setStateErrorByResponseCode(code: BackendResponseCode) {
        if (!code)
            return;
        switch (code) {
            case BackendResponseCode.CONFLICT: {
                this.errorOccurred = true;
                this.codeError = true;
                this.codeErrorPopover.showPopower(PopowerErrorMessages.INCORRECT_CODE);
                break;
            }
            case BackendResponseCode.NOT_FOUNT: {
                this.errorOccurred = true;
                this.codeError = true;
                this.codeErrorPopover.showPopower(PopowerErrorMessages.NOT_FOUNT);
                break;
            }
        }
    }
}