import { Component, OnInit, AfterViewInit, ViewChild } from '@angular/core';
import { ApiService } from '../../_services/api/api.service';
import { ToastrManager } from 'ng6-toastr-notifications';
import { Router, ActivatedRoute } from '@angular/router';
import { AddChildByCodeErrorViewModel } from 'src/app/_models/children/addChildByCodeErrorViewModel';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { BackendResponseCode } from 'src/app/_models/BackendResponseCodes';

@Component({
  selector: 'add-child-by-code',
  templateUrl: './add-child-by-code.component.html',
})

export class AddChildByCodeComponent implements OnInit {
  @ViewChild('addChildByCodeResult', { static: false }) private modalChildComponent;


  public errorViewModel: AddChildByCodeErrorViewModel = new AddChildByCodeErrorViewModel();
  public loading: boolean = false;

  public childId: string;

  public childFirstName: string;
  public childLastName: string;

  private base64regex = /^([0-9a-zA-Z+/]{4})*(([0-9a-zA-Z+/]{2}==)|([0-9a-zA-Z+/]{3}=))?$/;

  constructor(private apiService: ApiService,
    private toastr: ToastrManager,
    private router: Router, private activateRoute: ActivatedRoute, private modalService: NgbModal) {

  }

  ngOnInit(): void {
  }

  onChangeValue() {
    this.errorViewModel.setStateNoErrors();
  }

  onClickAddChildByCode() {
    this.loading = true;
    let decodedGuid;
    try {
      let decodeTest = this.base64regex.test(this.childId);
      if (!decodeTest)
        throw BackendResponseCode.CONFLICT;
        
      decodedGuid = atob(this.childId);

    } catch (ex) {
      this.errorViewModel.setStateErrorByResponseCode(BackendResponseCode.CONFLICT);
      this.loading = false;
      return;
    };

    this.apiService.postAddChildByCode(decodedGuid).subscribe((response) => {
      this.loading = false;

      this.apiService.getUser(decodedGuid).subscribe(response => {
        this.childFirstName = response.firstName;
        this.childLastName = response.lastName;

        this.openModal();
      })

    }, error => {
      this.errorViewModel.setStateErrorByResponseCode(error.status);
      this.loading = false;
    })
  }

  childrenRoute() {
    this.router.navigate(['/profile/children']);
  }

  openModal() {
    this.modalService.open(this.modalChildComponent, { ariaLabelledBy: 'modal-basic-title', centered: true, size: 'lg' }).result.then((result) => {
      this.childrenRoute()
    }, (reason) => {
      this.childrenRoute()
    });
  }
}
