import { Component, OnInit } from '@angular/core';
import { UserSignUpModel } from '../_models/sign-up/signUpModel'
import { ApiService } from '../_services/api/api.service';
import { AppConstants, PopowerErrorMessages, InputPatterns } from '../_models/AppConstants'
import { ToastrManager } from 'ng6-toastr-notifications';
import { SignUpErrorViewModel } from '../_models/sign-up/signUpModel'
import { ValidationService } from "../_services/helpers/validationService";
import { BackendResponseCode } from '../_models/backendResponseCodes'
import { Router } from '@angular/router';
import { ScriptLoaderService } from '../_services/script-loader.service';
import { Country, State } from '../_models/user/user';
import { ContentHelper } from '../_services/helpers/contentHelper';
import { LocalStorageService } from '../_services/localStorage.service';

declare let $: any;
@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['sign-up.component.scss']
})
export class SignUpComponent implements OnInit {
  public loading: boolean = false;
  public emailPattern = InputPatterns.EMAIL_PATTERN;
  public userSignUpModel: UserSignUpModel = new UserSignUpModel();
  public signUpErrorViewModel: SignUpErrorViewModel = new SignUpErrorViewModel();
  public countries: Array<Country>;
  public currentCountry: Country;
  public states: Array<State>;
  public country_USA: any;
  public USACountrySelected = false;

  constructor(private apiService: ApiService,
    private localStoregeService: LocalStorageService,
    private toastr: ToastrManager,
    private router: Router,
    private _script: ScriptLoaderService,) {
  }

  ngOnInit(): void {
    ContentHelper.setLoading(true);
    let countryUSA;
    this.apiService.getCountries().subscribe((response: any) => {
      this.countries = response.items;
      localStorage.setItem(AppConstants.LOCAL_STORAGE_COUNTRIES_KEY, JSON.stringify(response.items));
      this.country_USA = this.countries.find(x => x.alpha2Code === AppConstants.USA_COUNTRY_ALPHA2CODE);

      this.countries.forEach(c => {
        if (c.alpha2Code === AppConstants.USA_COUNTRY_ALPHA2CODE) {
          c.name = "United States";
          countryUSA = c;
        }
      });

      this.apiService.getStates().subscribe((response: any) => {
        this.states = response.items;
        localStorage.setItem(AppConstants.LOCAL_STORAGE_STATES_KEY, JSON.stringify(response.items));

        if(countryUSA){
          this.userSignUpModel.countryId = countryUSA.id;
          this.onCountrySelected(countryUSA.id);
        }
        ContentHelper.setLoading(false);
      }, error => {
        ContentHelper.setLoading(false);
      });
    }, error => {
      ContentHelper.setLoading(false);
    });

    var date_input = $('#dateOfBirth'); //our date input has the name "date"
    date_input.datepicker();
    date_input.change((event) => {
      this.userSignUpModel.dateOfBirthForView = date_input.val();
      this.formChangedBaseHandler()
    });

  }

  signUp(form: any) {
    if (!form.valid)
      return;
    this.loading = true;

    this.userSignUpModel.parseDateOfBirth();

    this.apiService.signUp(this.userSignUpModel).subscribe((response: any) => {

      if (!response.success) {
        this.signUpErrorViewModel.setStateErrorByResponseCode(response.code as BackendResponseCode);
        this.loading = false;
        return;
      }
      this.router.navigate(['/sign-up-verified']);
    }, error => {
      this.loading = false;
    });

  }


  formChangedBaseHandler() {
    this.checkFormFields();
  }

  emailChangeValue(event: any) {
    this.checkFormFields();
    if (this.userSignUpModel.email && !ValidationService.ifEmailMatchPattern(this.userSignUpModel.email)) {
      this.signUpErrorViewModel.emailErrorPopover.showPopower(PopowerErrorMessages.EMAIL_PATTERN_NOT_MATCHES);
      this.signUpErrorViewModel.emailError = true;
    }
  }

  passwordChangeValue() {
    this.checkFormFields();
    if (this.userSignUpModel.password && !ValidationService.ifPasswordMatchPattern(this.userSignUpModel.password)) {
      this.signUpErrorViewModel.passwordErrorPopover.showPopower(PopowerErrorMessages.PASSWORD_PATTERN_NOT_MATCHES);
      this.signUpErrorViewModel.passwordError = true;
    }
  }

  // Show fields with red color to show that field should be filled
  checkFormFields() {
    this.signUpErrorViewModel.setStateNoErrors();

    if (!this.userSignUpModel.email || !ValidationService.ifEmailMatchPattern(this.userSignUpModel.email))
      this.signUpErrorViewModel.emailError = true;

    if (!this.userSignUpModel.password || !ValidationService.ifPasswordMatchPattern(this.userSignUpModel.password))
      this.signUpErrorViewModel.passwordError = true;

    if (!this.userSignUpModel.phone)
      this.signUpErrorViewModel.phoneError = true;

    if (!this.userSignUpModel.firstName || !ValidationService.ifNameMatchPattern(this.userSignUpModel.firstName))
      this.signUpErrorViewModel.firstNameError = true;

    if (!this.userSignUpModel.lastName || !ValidationService.ifNameMatchPattern(this.userSignUpModel.lastName))
      this.signUpErrorViewModel.lastNameError = true;

    if (this.userSignUpModel.gender == undefined)
      this.signUpErrorViewModel.sexError = true;

    if (!this.userSignUpModel.dateOfBirthForView)
      this.signUpErrorViewModel.dateOfBirthError = true;

    if (!this.userSignUpModel.countryId)
      this.signUpErrorViewModel.countryError = true;

    if (!this.userSignUpModel.stateId)
      this.signUpErrorViewModel.stateError = true;

    if (!this.userSignUpModel.facilityCode)
      this.signUpErrorViewModel.facilityCodeError = true;

    if (!this.userSignUpModel.eula)
      this.signUpErrorViewModel.termsAndConditionError = true;

  }

  closeModalPrivacy() {
    var body = document.querySelector('body');
    body.classList.remove('modal-open-privacy');
  }

  openModalPrivacy() {
    var body = document.querySelector('body');
    body.classList.add('modal-open-privacy');
  }

  initModalPrivacyHandler() {
    var body = document.querySelector('body');
    var btnPrivacy = document.querySelectorAll('.btn-privacy-js');

    btnPrivacy.forEach(function (btn) {
      btn.addEventListener('click', function () {
        body.classList.add('modal-open-privacy');
      });
    });
  }

  onCountrySelected(value: string) {
    this.USACountrySelected = value === this.country_USA.id;
    this.userSignUpModel.stateId = undefined;
  }



}
