import { Injectable } from "@angular/core";
import { Child } from "../_models/children/child";
import { Country, State, User } from "../_models/user/user";

@Injectable()
export class LocalStorageService {
    private LOCAL_STORAGE_USER_KEY = 'currentUser';
    private LOCAL_STORAGE_CHILDREN_KEY = 'children';

    private LOCAL_STORAGE_COUNTRIES_KEY = "countries";
    private LOCAL_STORAGE_STATES_KEY = "states";

    verify() {
        return localStorage.getItem(this.LOCAL_STORAGE_USER_KEY);
    }

    setUser(user: User) {
        localStorage.setItem(this.LOCAL_STORAGE_USER_KEY, JSON.stringify(user))
    }

    updateUserTokens(accessToken: string, refreshToken: string) {
        let currentUser = JSON.parse(localStorage.getItem(this.LOCAL_STORAGE_USER_KEY));
        currentUser.token = accessToken;
        currentUser.refreshToken = refreshToken;
        localStorage.removeItem(this.LOCAL_STORAGE_USER_KEY);
        localStorage.setItem(this.LOCAL_STORAGE_USER_KEY, JSON.stringify(currentUser));
        console.log("Token updated")
    }

    getCountries() {
        let countries = JSON.parse(localStorage.getItem(this.LOCAL_STORAGE_COUNTRIES_KEY)) as Array<Country>;
        return countries;
    }

    getStates() {
        let states = JSON.parse(localStorage.getItem(this.LOCAL_STORAGE_STATES_KEY)) as Array<State>;
        return states;
    }

    setCountries(countries: Array<Country>) {
        localStorage.setItem(this.LOCAL_STORAGE_COUNTRIES_KEY, JSON.stringify(countries));
    }

    setStates(states: Array<State>) {
        localStorage.setItem(this.LOCAL_STORAGE_STATES_KEY, JSON.stringify(states));
    }

    getUser(): User {
        let user = JSON.parse(localStorage.getItem(this.LOCAL_STORAGE_USER_KEY)) as User;

        if (!this.userModelIsValid(user)) {
            localStorage.removeItem(this.LOCAL_STORAGE_USER_KEY);
            return undefined;
        }

        return user;
    }

    getChildren(): Array<Child> {
        return JSON.parse(localStorage.getItem(this.LOCAL_STORAGE_CHILDREN_KEY)) as Array<Child>;
    }

    getChildById(id: string): Child {
        if (!id)
            throw "[getChildById]: Id not valid!";

        let children = JSON.parse(localStorage.getItem(this.LOCAL_STORAGE_CHILDREN_KEY)) as Array<Child>;
        let child = children.find((item) => { return item.id === id });
        if (!child)
            throw "[getChildById]: Child not found!";

        return child;
    }

    setChildren(children: Array<Child>) {
        localStorage.setItem(this.LOCAL_STORAGE_CHILDREN_KEY, JSON.stringify(children))
    }

    private userModelIsValid(user: User): boolean {
        if (!user)
            return false;

        if (user.id === undefined && (user as any).userId !== undefined) {
            user.id = (user as any).userId
            this.setUser(user);
        }

        if (!user.id)
            return false;
        if (!user.token)
            return false;
        if (!user.refreshToken)
            return false;
        if (!user.email)
            return false;

        return true;
    }
}
