import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../_services/api/api.service';
import { AppConstants} from '../../_models/AppConstants'
import { ToastrManager } from 'ng6-toastr-notifications';
import { ValidationService } from "../../_services/helpers/validationService";
import { Router, ActivatedRoute } from '@angular/router';
import { Country, State } from '../../_models/user/user';
import { Subscription } from 'rxjs';
import { CreateChildErrorViewModel } from 'src/app/_models/children/createChildErrorViewModel';
import { ContentHelper } from 'src/app/_services/helpers/contentHelper';
import { ChildMapper } from 'src/app/_services/helpers/childMapper';
import { EditChildModel } from 'src/app/_models/children/editChildModel';
import { LocalStorageService } from 'src/app/_services/localStorage.service';
import { Child } from 'src/app/_models/children/child';
import { ApiBaseRequests } from 'src/app/_services/helpers/apiBaseRequests';

declare let $: any;

@Component({
  selector: 'edit-children-profile',
  templateUrl: './edit-child-profile.component.html',
})

export class EditChildProfileComponent implements OnInit {

  public child: Child = new Child();
  public editChildModel: EditChildModel = new EditChildModel();
  public editProfileErrorViewModel: CreateChildErrorViewModel = new CreateChildErrorViewModel();

  public loading: boolean = false;
  public countries: Array<Country>;
  public states: Array<State>;
  public country_USA: any;
  public USACountrySelected = false;

  private subscription: Subscription;
  public childId: string;

  constructor(private apiService: ApiService,
    private toastr: ToastrManager,
    private router: Router,
    private activateRoute: ActivatedRoute,
    private localStorageService: LocalStorageService,
    private apiBaseRepository: ApiBaseRequests) {

    this.subscription = activateRoute.params.subscribe(params => {
      this.childId = atob(params['id']);
    });

  }

  ngOnInit(): void {
    let user = this.localStorageService.getUser();
    if (!user){
      this.router.navigate(['/login']);
      return;
    }

    ContentHelper.setLoading(true);

    this.countries = this.localStorageService.getCountries();
    this.states = this.localStorageService.getStates();

    if (!this.countries || !this.states) {
      this.apiBaseRepository.getCountriesStates().then(countriesStates => {
        this.countries = countriesStates.countries;
        this.states = countriesStates.states;
        this.finalizeNgOnInit();
      });
    }else{
      this.finalizeNgOnInit();
    }
  }

  private finalizeNgOnInit() {
    this.country_USA = this.countries.find(x => x.alpha2Code == AppConstants.USA_COUNTRY_ALPHA2CODE);

    this.apiService.getUser(this.childId).subscribe((getUserResponse) => {
      this.child = ChildMapper.mapGetUserByIdResponseToChildModel(this.childId, getUserResponse);
      this.editChildModel = ChildMapper.mapChildToEditChildMoldel(this.child);
      this.initEditProfileModels();
      ContentHelper.setLoading(false);
    });
  }

  initEditProfileModels() {

    var date_input = $('#dateOfBirth');
    date_input.datepicker({
      orientation: "bottom auto",
      startDate: "01/01/1990",
      endDate: "new Date()",
    });

    date_input.datepicker("setDate", this.editChildModel.dateOfBirth);
    this.editChildModel.dateOfBirth = date_input.val();

    date_input.change((event) => {
      this.editChildModel.dateOfBirth = date_input.val();
      this.formChangedBaseHandler()
    });

    this.onCountrySelected(this.editChildModel.countryId);
    this.checkFormFields();
    this.loading = false;
  }

  formChangedBaseHandler() {
    this.checkFormFields();
  }

  checkFormFields() {
    this.editProfileErrorViewModel.setStateNoErrors();

    if (!this.editChildModel.firstName || !ValidationService.ifNameMatchPattern(this.editChildModel.firstName))
      this.editProfileErrorViewModel.firstNameError = true;

    if (!this.editChildModel.lastName || !ValidationService.ifNameMatchPattern(this.editChildModel.lastName))
      this.editProfileErrorViewModel.lastNameError = true;

    if (this.editChildModel.gender == undefined)
      this.editProfileErrorViewModel.sexError = true;

    if (!this.editChildModel.dateOfBirth)
      this.editProfileErrorViewModel.dateOfBirthError = true;

    if (!this.editChildModel.countryId)
      this.editProfileErrorViewModel.countryError = true;

    if (!this.editChildModel.stateId)
      this.editProfileErrorViewModel.stateError = true;

  }

  onCountrySelected(value: string) {
    this.USACountrySelected = value === this.country_USA.id;
    if (!this.USACountrySelected)
      this.editChildModel.stateId = undefined;
  }

  saveChanges() {
    this.loading = true;
    let body = ChildMapper.mapEditChildRequest(this.editChildModel);

    this.apiService.updateChildProfile(this.child.id, body).subscribe((response: any) => {
      this.loading = false;
      if (response.success) {
        this.toastr.successToastr("Profile updated successfully", 'Success!');
        this.router.navigate(['/profile/children']);
      } else {
        this.toastr.errorToastr(response.errors.join(','), 'Error');
      }
    }, error => {
      this.toastr.errorToastr('Error occurred', 'Oops!');
      this.loading = false;
    })
  }
}
