import html2canvas from 'html2canvas';
import { CardResultTemplates } from "../_models/cardResultTemplates";
import { HealthCheckResult } from "../_models/AppConstants";
import { QRCodeDataModel } from '../_models/health-test/HealthCheckModel';

export class QRCodeService {

    static generateQRCode(
        dynamicLink: string): QRCodeData {

        let qrCodeData = new QRCodeData();

        qrCodeData.elementType = "img";
        qrCodeData.level = "M";
        qrCodeData.qrdata = dynamicLink;
        qrCodeData.scale = 1;
        qrCodeData.width = 256;
        return qrCodeData;
    }
}


export class QRCodeData {
    public qrdata: string = "";
    public elementType: "img" | "url" | "canvas" | "svg"  = "img";
    public level: "L" | "M" | "Q" | "H" = "M";
    public scale: number = 1;
    public width: number = 256;
}
