import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { HealthTestViewModel, ViewState } from '../_models/health-test/viewModels';
import { LocalStorageService } from '../_services/localStorage.service';
import { User, Facility } from '../_models/user/user';
import { ApiService } from "../_services/api/api.service";
import { ToastrManager } from 'ng6-toastr-notifications';
import { GetUserByIdResponseModel } from '../_models/api/responseModel/getUserByIdResponseModel';
import { CustomSurveyWizard } from '../_services/customQuestionWizard/customSurveyWizard.service';
import { ChildBaseModel } from '../_models/children/child';
import { LanguageService } from '../_services/language.service';
import { CheckResponseModel } from '../_models/api/responseModel/checkResponseModel';
import { CheckModelDTO } from '../_models/health-test/HealthCheckModel';
import { ProgressBarViewModel } from '../_services/customQuestionWizard/CustomSurveyModels';

@Component({
  selector: 'app-health-test',
  templateUrl: './health-test.component.html',
  styleUrls: ['health-test.component.scss']
})
export class HealthTestComponent implements OnInit {
  public healthTestViewModel: HealthTestViewModel = new HealthTestViewModel();
  private customSurveyWizard: CustomSurveyWizard

  public selectFacility: string;
  public facilityList: Array<Facility> = [];
  public facilityListIsEmpty: boolean = false;

  public currentQuestion: any;
  public progressBarViewModel: ProgressBarViewModel;
  public user: User;
  public children: Array<ChildBaseModel>;

  public currentIdentity: GetUserByIdResponseModel;
  public currentFacility: Facility;

  constructor(private _router: Router,
    private localStorageService: LocalStorageService,
    private apiService: ApiService,
    private toastr: ToastrManager,
    private languageService: LanguageService) {
  }

  ngOnInit(): void {
    this.healthTestViewModel.setStateLoading()
    this.user = this.localStorageService.getUser();

    if (!this.user) {
      this._router.navigate(['/login']);
      return;
    }

    this.apiService.getChildren().subscribe((response) => {
      this.children = response.items;

      if (!this.children || this.children.length == 0) {
        this.chooseIdentity(this.user);
      } else {
        this.healthTestViewModel.setStateChooseIdentity();
      }
    });
  }

  chooseIdentity(identity: User | ChildBaseModel) {
    this.healthTestViewModel.setStateLoading();

    this.apiService.getUser(identity.id).subscribe(getUserByIdResponse => {
      this.currentIdentity = getUserByIdResponse;
      this.currentIdentity.id = identity.id;
      this.facilityList = this.currentIdentity.facilities;

      this.healthTestViewModel.setStateChooseFacility();
      this.fillOutFacilitySelectInput()
    })
  }

  private fillOutFacilitySelectInput() {
    if (this.facilityList && this.facilityList.length === 1) {
      this.selectFacility = this.facilityList[0].facilityId;
      this.onChangeSelectFalilityDropdown()
      return;
    }

    if (!this.facilityList || this.facilityList.length === 0) {
      this.facilityListIsEmpty = true;
      return;
    }

    this.selectFacility = null;
  }

  private updateViewModels() {
    this.currentQuestion = this.customSurveyWizard.getCurrentQuestion();
    this.progressBarViewModel = this.customSurveyWizard.getProgressBarViewModel();
  }

  chooseFacility() {
    if (this.healthTestViewModel.questionsListIsEmpty)
      return;
    this.initCustomSurveyWizard(this.currentFacility.questions)
    this.healthTestViewModel.setStateStartWizard();
  }

  private initCustomSurveyWizard(questions: any) {
    this.customSurveyWizard = new CustomSurveyWizard(questions, this.languageService.getCurrentLanguage());

    this.customSurveyWizard.startWizard();
    this.currentQuestion = this.customSurveyWizard.getCurrentQuestion();
    this.progressBarViewModel = this.customSurveyWizard.getProgressBarViewModel();
  }

  onChangeSelectFalilityDropdown() {
    if (!this.currentIdentity || !this.currentIdentity.facilities)
      return;

    this.healthTestViewModel.questionsListIsEmpty = false;
    this.currentFacility = this.currentIdentity.facilities.find(x => { return x.facilityId === this.selectFacility });
    this.healthTestViewModel.questionsListIsEmpty =
      !(this.currentFacility && this.currentFacility.questions && this.currentFacility.questions.length > 0);
  }

  handleAnswer(answer: any) {
    this.customSurveyWizard.handleAnswer(answer);
    this.moveForward();

    if (document.activeElement instanceof HTMLElement)
      document.activeElement.blur();
  }

  private moveForward() {
    this.customSurveyWizard.nextQuestion();
    this.updateViewModels();

    if (this.customSurveyWizard.ifFinished()) {
      this.healthTestViewModel.setStateCheckRersult();
      this.finishTheTest();
    }
  }

  moveBack() {
    if (this.customSurveyWizard.nowIsFirstQuestion()) {
      this.healthTestViewModel.setStateChooseFacility();
      this.customSurveyWizard.startWizard();
      this.fillOutFacilitySelectInput();
      this.updateViewModels();
      return;
    }

    this.customSurveyWizard.previousQuestion();
    this.updateViewModels();
  }

  private finishTheTest() {
    this.healthTestViewModel.setStateLoading();
    let requestModel = this.customSurveyWizard.getSurveyAnswers(
      this.currentIdentity.id,
      this.currentFacility.id,
      this.currentFacility.surveyId);

    this.apiService.checkTest(requestModel).subscribe((response: CheckResponseModel) => {
      this.navigateToTheTestResult(response);
    }, error => {
      console.error(error);
      this.toastr.errorToastr('Error occurred!<br><br><p>Please, try again in 10 minutes!</p>', 'Oops!', { enableHTML: true, dismiss: "click", showCloseButton: true });
      this.chooseIdentity(this.currentIdentity);
    })
  }

  private navigateToTheTestResult(response: CheckResponseModel) {
    let checkModelDTO = new CheckModelDTO();

    checkModelDTO.id = response.id;
    checkModelDTO.result = response.result;
    checkModelDTO.timestamp = response.timestamp;

    checkModelDTO.facilityId = this.currentFacility.id;
    checkModelDTO.facilityName = this.currentFacility.facilityName;
    checkModelDTO.facilityId = this.currentFacility.facilityId;
    checkModelDTO.userFirstName = this.currentIdentity.firstName;
    checkModelDTO.userLastName = this.currentIdentity.lastName;
    checkModelDTO.userId = this.currentIdentity.id;

    localStorage.setItem('checkModel', JSON.stringify(checkModelDTO));
    this._router.navigate(['/health-check-result']);
  }
}
