import { Component, OnInit, Console } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { User, SignInErrorViewModel, Country, State, UserLoginModel, TemperatureScale } from '../_models/user/user';
import { BackendResponseCode } from '../_models/BackendResponseCodes';
import { AuthenticationService } from '../_services/auth.service'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ToastrManager } from 'ng6-toastr-notifications';
import { ActivationRequestModel, ActivationViewModel } from "../_models/sign-in/signInModels";
import { ContentHelper } from "../_services/helpers/contentHelper";
import { ReturnStatement } from '@angular/compiler';
import { environment } from 'src/environments/environment';
import { ApiService } from '../_services/api/api.service';
import { GetChildrenResponseModel } from '../_models/api/responseModel/getChildrenResponseModel';
import { Child } from '../_models/children/child';
import { GetUserByIdResponseModel } from '../_models/api/responseModel/getUserByIdResponseModel';
import { ChildMapper } from '../_services/helpers/childMapper';
import { LocalStorageService } from '../_services/localStorage.service';
import { ApiBaseRequests } from '../_services/helpers/apiBaseRequests';



declare let $: any;
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['login.component.scss']
})
export class LoginComponent implements OnInit {
  public userLoginModel: UserLoginModel = new UserLoginModel();
  public signInErrorViewModel: SignInErrorViewModel = new SignInErrorViewModel();
  public activationViewModel = new ActivationViewModel()

  public loading: boolean;

  public content: any;

  constructor(private _router: Router,
    private authService: AuthenticationService,
    private modalService: NgbModal,
    private toastr: ToastrManager,
    private route: ActivatedRoute,
    private apiService: ApiService,
    private localStorageService: LocalStorageService, 
    private apiHelper: ApiBaseRequests) {
    this.loading = false;
  }

  ngOnInit(): void {

    this.route.queryParams.subscribe(params => {
      let activationModel = new ActivationRequestModel()

      activationModel.userId = params['id'];
      activationModel.token = params['code'];

      if (!activationModel.isValid()) {
        this._router.navigate(['/home']);
        return;
      }

      this.loading = true;
      this.activationViewModel.setStateActivationInProgress();

      ContentHelper.showEmailVerifyModal();

      this.authService.confirmEmailToken(activationModel).subscribe((response: any) => {
        this.loading = false;

        this.activationViewModel.setStateRequestReceived(response.success);
      }, error => {
        this.loading = false;
        this.activationViewModel.setStateRequestReceived(false);
      });
    });
  }

  formChangeValue(event: any) {
    this.signInErrorViewModel.setStateNoError();
  }

  signIn(form: NgForm) {
    this.loading = true;

    this.authService.login(this.userLoginModel).subscribe(async (response: any) => {

      if (!response.success) {
        this.loading = false;
        this.handleErrorResponseCode(response);
        return;
      }

      this.mapUser(response);

      await this.apiHelper.getChildren();
      this._router.navigate(['/home']);
    },
      error => {
        this.toastr.errorToastr(`Error occurred! <br>${error.message}`, 'Oops!', { enableHTML: true, dismiss: "click", showCloseButton: true });
        this.loading = false;
      });
  }

  private handleErrorResponseCode(response: any) {
    let code = response.code as BackendResponseCode;
    switch (code) {
      case BackendResponseCode.USER_NOT_ACTIVE: {
        this.signInErrorViewModel.emailError = true;
        this.signInErrorViewModel.userNotActiveError = true;
        break;
      }
      case BackendResponseCode.PASSWORD_NOT_VALID: {
        this.signInErrorViewModel.passwordError = true;
        this.signInErrorViewModel.passwordError = true;
        break;
      }
      case BackendResponseCode.USER_NOT_FOUND: {
        this.signInErrorViewModel.emailError = true;
        this.signInErrorViewModel.userNotFountError = true;
        break;
      }
    }
  }

  private mapUser(loginResponse: any) {

    let user: User =
    {
      token: loginResponse.token,
      refreshToken: loginResponse.refreshToken,
      id: loginResponse.userId,
      email: loginResponse.email,
      age: loginResponse.age,
      firstName: loginResponse.firstName,
      lastName: loginResponse.lastName,
      phone: loginResponse.phone,
      userFacility: loginResponse.userFacility,
      country: loginResponse.country,
      state: loginResponse.state,
      availableTemperatureScales: loginResponse.availableTemperatureScales,
      gender: loginResponse.gender,
      temperatureScale: loginResponse.temperatureScale,
      dateOfBirth: loginResponse.dateOfBirth
    };
    this.setDefaultTemperatureScaleIfNecessary(user);
    this.localStorageService.setUser(user);
  }

  private setDefaultTemperatureScaleIfNecessary(user: User) {
    if (user.temperatureScale)
      return;
    if (!user.availableTemperatureScales && user.availableTemperatureScales.length == 0) {
      console.error("TemperatureScalesList is empty. It is impossible to set default temperature scale to user");
      return;
    }
    let defaultTemperatureFahrenheit = user.availableTemperatureScales.find(x => x.name == 'Fahrenheit');
    if (defaultTemperatureFahrenheit)
      user.temperatureScale = defaultTemperatureFahrenheit;
    else {
      console.error("Default TemperatureScale by alias not found");
      user.temperatureScale = new TemperatureScale();
      user.temperatureScale.name = 'Fahrenheit';
      user.temperatureScale.id = '';
    }
  }

  closeModalVerify() {
    if (this.activationViewModel.activationInProgress)
      return;
    ContentHelper.closeEmailVerifyModal();
    this._router.navigate(['/home']);
  }
}
