export class HealthTestViewModel {
    viewState: ViewState;
    public questionsListIsEmpty : boolean = false;

    setStateChooseIdentity(){
        this.viewState = ViewState.CHOOSE_IDENTITY;
    }

    setStateChooseFacility(){
        this.viewState = ViewState.CHOOSE_FACILITY;
    }

    setStateStartWizard(){
        this.viewState = ViewState.QUESTION_WIZARD;
    }

    setStateCheckRersult(){
        this.viewState = ViewState.CHECK_RESULT;
    }

    setStateLoading(){
        this.viewState = ViewState.LOADING;
    }
}

export enum ViewState {
    LOADING = 'LOADING',
    CHOOSE_IDENTITY ='CHOOSE_IDENTITY',
    CHOOSE_FACILITY = 'CHOOSE_FACILITY',
    QUESTION_WIZARD = 'QUESTION_WIZARD',
    CHECK_RESULT = 'CHECK_RESULT',
  }