import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ActivationViewModel } from "../_models/sign-in/signInModels";
import { ContentHelper } from "../_services/helpers/contentHelper";
import { DataProviderService } from '../_services/data-provider/data-provider.service';


declare let $: any;
@Component({
  selector: 'app-news',
  templateUrl: './news.component.html',
  styleUrls: ['news.component.scss']
})
export class NewsComponent implements OnInit {
  public loading: boolean;

  public content: any;

  public news = [];

	public lastPageReached = false;

	public fireNews: any[] = [];
	public fireNewsRows: any[] = [];

	public dashboardItem = "dashboardItem";
	public breakpoint: number = 3;

  constructor(private _router: Router, private dateProvider: DataProviderService) {
    this.loading = false;
  }

  ngOnInit(): void {
		this.loading = true;
    this.dateProvider.init().then(() => {
			this.dateProvider.getAll().then(res => {
				this.fireNews = res;
				//this.fireNewsRows = DataProviderExtension.splitSequenceToRows(res, 3);
				this.loading = false;
			});
		});
  }

  onClickOpenItem(item){
    console.log("Open item");
    console.log(item);
		this._router.navigate(['/article', item.id], { state: item });
  }
}
