import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { User } from '../_models/user/user';
import { ApiService } from '../_services/api/api.service';
import { DataSharingService } from '../_services/data-sharing.service';
import { ApiBaseRequests } from '../_services/helpers/apiBaseRequests';
import { LocalStorageService } from '../_services/localStorage.service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {

  public user: User;
  public hasChildren: boolean = false;

  constructor(private _router: Router, private localStorageService: LocalStorageService,
    private dataSharingService: DataSharingService,
    private apiService: ApiService,
    private apiHelper: ApiBaseRequests) {
  }

  ngOnInit(): void {
    this.user = this.localStorageService.getUser();
    if (!this.user) {
      this._router.navigate(['/login']);
      return;
    }

    this.getChildrenfromBackend();
  }

  getChildrenfromBackend() {
    this.apiService.getChildren().subscribe((response) => {
      if (!response.items) {
        this.hasChildren = false;
        return;
      }
      this.hasChildren = response.items.length > 0;
    });
  }
}
