import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import {UserLoginModel} from '../_models/user/user'
import { ActivationRequestModel } from "../_models/sign-in/signInModels";
import { AppConstants } from "../_models/AppConstants";
import { environment } from "src/environments/environment";
import { Observable } from "rxjs";


@Injectable()
export class AuthenticationService {

    constructor(private http: HttpClient) {
    }

    login(loginModel: UserLoginModel) {

        let body = JSON.stringify({ userName: loginModel.email, password: loginModel.password });

        return this.http.post(environment.apiUrl + '/api/auth/login', {userName: loginModel.email, password: loginModel.password});
    }

    confirmEmailToken(body: ActivationRequestModel) {
        return this.http.post(environment.apiUrl + '/api/auth/confirm-email-token', body);
    }

    refreshToken(params: any) {
        return this.http.post(environment.apiUrl + '/api/Auth/refresh', params);
    }
}