import { PopowerErrorViewModel } from "../popowerErrorViewModel";
import { FacilityModelDTO } from "../user/user";


export class AddFacilityViewModel {

    public showAddFacilityDialog: boolean;
    public newFacilityCode: string;
    public loading: boolean;

    public facilityErrorPopover: PopowerErrorViewModel = new PopowerErrorViewModel()

    public setStateNoErrors() {
        this.facilityErrorPopover.hidePopower();
    }

    public setAddFacilityDialogIsActive(state: boolean) {
        this.loading = false;
        this.newFacilityCode = null;
        this.facilityErrorPopover.hidePopower();
        this.showAddFacilityDialog = state;
    }

    handleResponse(response: any) {
        if (!response.success) {
            this.facilityErrorPopover.showPopower("Facility not found")
        }
    }

    facilityValueChanged() {
        this.setStateNoErrors();
    }

    onClickAddFacilityOpenDialog(event: Event) {
        this.setAddFacilityDialogIsActive(true);
        setTimeout(() => $('#inputFacilityCode').focus(), 200);
    }

    closeDialogAddFacility() {
        if (!this.loading)
            this.setAddFacilityDialogIsActive(false);
    }
}

export class FacilityResponseModel {
    success: boolean;
    message: string
    errors: Array<string>
    facility: FacilityModelDTO
}