import { Component, OnInit } from '@angular/core';
import { User } from 'src/app/_models/user/user';
import { AddFacilityViewModel, FacilityResponseModel } from 'src/app/_models/profile/addFacilityViewModel';
import { ApiService } from "../../_services/api/api.service";
import { AppConstants, PopowerErrorMessages } from 'src/app/_models/AppConstants';
import { ChangePasswordViewModel } from 'src/app/_models/profile/changePasswordViewModel';
import { ValidationService } from 'src/app/_services/helpers/validationService';
import { ToastrManager } from 'ng6-toastr-notifications';
import { LocalStorageService } from 'src/app/_services/localStorage.service';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ChildBaseModel } from '../../_models/children/child';
import { ApiBaseRequests } from 'src/app/_services/helpers/apiBaseRequests';
import { ContentHelper } from 'src/app/_services/helpers/contentHelper';

@Component({
  selector: 'app-children',
  templateUrl: './children.component.html',
  styleUrls: ['children.component.scss']
})
export class ChildrenComponent implements OnInit {

  public user: User;
  public addFacilityDialogViewModel = new AddFacilityViewModel();
  public changePasswordViewModel = new ChangePasswordViewModel();
  public loading: boolean = false;

  public children: Array<ChildBaseModel> = [];

  constructor(private apiService: ApiService,
    private toastr: ToastrManager,
    private localStorageService: LocalStorageService,
    private _router: Router,
    private modalService: NgbModal,
    private apiHelper: ApiBaseRequests) {
  }

  ngOnInit(): void {
    this.loading = true;

    this.user = this.localStorageService.getUser();
    if (!this.user) {
      this._router.navigate(['/login']);
      return;
    }
    
    this.apiHelper.getChildren().then(() => {
      this.children = this.localStorageService.getChildren();
      this.loading = false;
      if (this._router.url === "/profile/children")
        ContentHelper.chooseTabMyProfileChildren();
      if (!this.children || this.children.length == 0) {
        this._router.navigate(['/profile/my-profile']);
        return;
      }
    })
  }

  onClickAddFacilityOpenDialog(event: Event) {
    this.addFacilityDialogViewModel.setAddFacilityDialogIsActive(true);

    setTimeout(() => $('#inputFacilityCode').focus(), 200);
  }

  onClickAddFacilityInput() {
    if (!this.addFacilityDialogViewModel.newFacilityCode || this.addFacilityDialogViewModel.facilityErrorPopover.popowerIsVisible)
      return;
    this.addFacilityDialogViewModel.loading = true;

    let getFacilityResponseBody = { facilityCode: this.addFacilityDialogViewModel.newFacilityCode };

    this.apiService.getFacilityCode(getFacilityResponseBody).subscribe((response: FacilityResponseModel) => {

      if (!response.success) {
        this.addFacilityDialogViewModel.handleResponse(response);
        this.addFacilityDialogViewModel.loading = false;
        return;
      }

      let body = { userId: this.user.id, facilityCode: this.addFacilityDialogViewModel.newFacilityCode };
      this.apiService.addFacility(body).subscribe((response: any) => {
        this.user.userFacility = response;
        localStorage.setItem(AppConstants.LOCAL_STORAGE_USER_KEY, JSON.stringify(this.user));
        this.addFacilityDialogViewModel.loading = false;
        this.addFacilityDialogViewModel.setAddFacilityDialogIsActive(false);
      });
    });
  }

  facilityValueChanged() {
    this.addFacilityDialogViewModel.setStateNoErrors();
  }

  onClickOpenChangePasswordDialog() {
    this.changePasswordViewModel.setChangePasswordDialogisActive(true);
    setTimeout(() => $('#password').focus(), 200);
  }

  changePassword(isValid: boolean) {
    if (!isValid)
      return;
    this.changePasswordViewModel.loading = true;
    let body = { newPassword: this.changePasswordViewModel.newPassword }

    this.apiService.updatePassword(body).subscribe((response: any) => {
      this.changePasswordViewModel.setChangePasswordDialogisActive(false);
      if (response.success) {
        this.toastr.successToastr(`Password updated successfully`, 'Success!', { enableHTML: true, showCloseButton: true });
      }
      else
        this.changePasswordViewModel.passwordErrorPopover.showPopower(response.errors.join(','));
    },
      error => {
        this.toastr.errorToastr('Error occurred', 'Oops!');
      });
  }

  onChangeNewPassword() {
    this.changePasswordViewModel.setStateNoErrors();

    if (!this.changePasswordViewModel.newPassword || !ValidationService.ifPasswordMatchPattern(this.changePasswordViewModel.newPassword))
      this.changePasswordViewModel.passwordErrorPopover.showPopower(PopowerErrorMessages.PASSWORD_PATTERN_NOT_MATCHES);

  }

  closeDialogAddFacility() {
    if (!this.addFacilityDialogViewModel.loading)
      this.addFacilityDialogViewModel.setAddFacilityDialogIsActive(false);
  }

  closeDialogChangePassword() {
    if (!this.changePasswordViewModel.loading)
      this.changePasswordViewModel.setChangePasswordDialogisActive(false);
  }

  onClickCreateChildRoute() {
    this._router.navigate(['/add-child']);
  }

  onClickAddChildByCodeRoute() {
    this._router.navigate(['/add-child-by-code']);
  }

  onClickEditChild(id: any) {
    this._router.navigate(['/edit-children-profile']);
  }

  openModalAddChild(addchildmodal) {
    this.modalService.open(addchildmodal, { centered: true, size: 'lg' });
  }

}
