import { Injectable } from "@angular/core";
import { AppComponentViewModel, FooterTheme, HeaderType } from '../_models/app-component/viewModels'
import { ContentHelper } from "./helpers/contentHelper";

enum RouteEnum {
    SIGN_UP,
    LOGIN,
    RESTORE_PASSWORD,
    HOME,
    CHOOSE_FACILITY,
    QUESTION_WIZARD,
    MY_PROFILE,
    MY_RESULT,
    UNDEFINED
}


@Injectable()
export class RouteHandlerService {

    private appComponentModel: AppComponentViewModel = new AppComponentViewModel();

    getViewModel(): AppComponentViewModel {
        return this.appComponentModel;
    }

    handleContentByRoute(routeUrl: string) {
        if (routeUrl === '/') {
            this.defaultState();
            return;
        }
        
        this.defaultState();
    }

    private signUpRoute() {
        this.appComponentModel.backgroundPrimaryIsActive = true;
        this.appComponentModel.footerTheme = FooterTheme.FOOTER_THEME_WHITE_BACKGROUND;
        this.appComponentModel.headerType = HeaderType.HEADER_NOT_AUTHORIZED;
    }

    private loginRoute() {
        this.appComponentModel.backgroundPrimaryIsActive = false;
        this.appComponentModel.footerTheme = FooterTheme.FOOTER_THEME_WHITE_BACKGROUND;
        this.appComponentModel.headerType = HeaderType.HEADER_NOT_AUTHORIZED;
    }

    private restorePasswordRoute() {
        this.appComponentModel.backgroundPrimaryIsActive = true;
        this.appComponentModel.footerTheme = FooterTheme.FOOTER_THEME_WHITE_BACKGROUND;
        this.appComponentModel.headerType = HeaderType.HEADER_NOT_AUTHORIZED;
    }

    private defaultState() {
        this.appComponentModel.backgroundPrimaryIsActive = false;
        this.appComponentModel.footerTheme = FooterTheme.FOOTER_THEME_WHITE_BACKGROUND;
        this.appComponentModel.headerType = HeaderType.HEADER_NOT_AUTHORIZED;
    }

    private homeRoute() {
        this.appComponentModel.backgroundPrimaryIsActive = false;
        this.appComponentModel.footerTheme = FooterTheme.FOOTER_THEME_PRIMARY_BACKGROUND;
        this.appComponentModel.headerType = HeaderType.HEADER_AUTHORIZED;
    }

    private chooseFacillityForTestRoute() {
        this.appComponentModel.backgroundPrimaryIsActive = true;
        this.appComponentModel.footerTheme = FooterTheme.FOOTER_THEME_PRIMARY_BACKGROUND;
        this.appComponentModel.headerType = HeaderType.HEADER_AUTHORIZED;
    }

    private questionWizardRoute() {
        this.appComponentModel.backgroundPrimaryIsActive = true;
        this.appComponentModel.footerTheme = FooterTheme.FOOTER_THEME_WHITE_BACKGROUND;
        this.appComponentModel.headerType = HeaderType.HEADER_AUTHORIZED;
    }

    private profileRoute() {
        this.appComponentModel.backgroundPrimaryIsActive = false;
        this.appComponentModel.footerTheme = FooterTheme.FOOTER_THEME_WHITE_BACKGROUND;
        this.appComponentModel.headerType = HeaderType.HEADER_AUTHORIZED;
    }

    private checkResultRoute() {
        this.appComponentModel.backgroundPrimaryIsActive = true;
        this.appComponentModel.footerTheme = FooterTheme.FOOTER_THEME_WHITE_BACKGROUND;
        this.appComponentModel.headerType = HeaderType.HEADER_AUTHORIZED;
    }

    private myProfileRoute() {
        this.appComponentModel.backgroundPrimaryIsActive = false;
        this.appComponentModel.footerTheme = FooterTheme.FOOTER_THEME_WHITE_BACKGROUND;
        this.appComponentModel.headerType = HeaderType.HEADER_AUTHORIZED;

        var profileTabNav = document.querySelectorAll('.tabs__link');

        profileTabNav.forEach(function (item) {
            item.classList.remove('tabs__link--active');
        });

        var tab = document.querySelector('.my-profile-tab-js');
        tab.classList.add('tabs__link--active');

    }

    private childrenRoute() {
        this.appComponentModel.backgroundPrimaryIsActive = false;
        this.appComponentModel.footerTheme = FooterTheme.FOOTER_THEME_WHITE_BACKGROUND;
        this.appComponentModel.headerType = HeaderType.HEADER_AUTHORIZED;
        ContentHelper.chooseTabMyProfileChildren()
    }

    private myResultRoute() {
        this.appComponentModel.backgroundPrimaryIsActive = false;
        this.appComponentModel.footerTheme = FooterTheme.FOOTER_THEME_WHITE_BACKGROUND;
        this.appComponentModel.headerType = HeaderType.HEADER_AUTHORIZED;

        var profileTabNav = document.querySelectorAll('.tabs__link');

        profileTabNav.forEach(function (item) {
            item.classList.remove('tabs__link--active');
        });

        var tab = document.querySelector('.my-result-tab-js');
        tab.classList.add('tabs__link--active');
    }

    private editProfileRoute(){
        this.appComponentModel.backgroundPrimaryIsActive = true;
        this.appComponentModel.footerTheme = FooterTheme.FOOTER_THEME_WHITE_BACKGROUND;
        this.appComponentModel.headerType = HeaderType.HEADER_AUTHORIZED;
    }

}

