import { PopowerErrorViewModel } from "../popowerErrorViewModel";

export class ChangePasswordViewModel {

    public showChangePasswordDialog: boolean;
    public newPassword: string;
    public loading: boolean;
    public passwordInputType: string;
    public passwordErrorPopover: PopowerErrorViewModel = new PopowerErrorViewModel()

    public changePasswordIconType: string;

    constructor() {
        this.passwordInputType = "password";
        this.changePasswordIconType = ChangaPasswordInputIcon.BASE.toString();
    }

    public setStateNoErrors() {
        this.passwordErrorPopover.hidePopower();
    }

    setPasswordVisible() {
        if (this.passwordInputType == "password"){

            this.passwordInputType = "text";
            this.changePasswordIconType = ChangaPasswordInputIcon.CROSSED.toString();
        }
        else{
            this.passwordInputType = "password"
            this.changePasswordIconType = ChangaPasswordInputIcon.BASE.toString();
        }
    }

    public setChangePasswordDialogisActive(state: boolean) {
        this.loading = false;
        this.newPassword = null;
        this.passwordErrorPopover.hidePopower();
        this.showChangePasswordDialog = state;
    }

    handleResponse(response: any) {
        if (!response.success) {
            this.passwordErrorPopover.showPopower("Bad password")
        }
    }
}

export enum ChangaPasswordInputIcon{
    BASE = "BASE",
    CROSSED = "CROSSED"
}