import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../_services/api/api.service';
import { AppConstants} from '../../_models/AppConstants'
import { ToastrManager } from 'ng6-toastr-notifications';
import { ValidationService } from "../../_services/helpers/validationService";
import { Router } from '@angular/router';
import { User, Country, State } from '../../_models/user/user';
import { CreateChildModel } from '../../_models/children/createChildModel';
import { CreateChildErrorViewModel } from 'src/app/_models/children/createChildErrorViewModel';
import { ContentHelper } from 'src/app/_services/helpers/contentHelper';
import { ChildMapper } from 'src/app/_services/helpers/childMapper';
import { LocalStorageService } from 'src/app/_services/localStorage.service';
import { DataSharingService } from 'src/app/_services/data-sharing.service';
import { ApiBaseRequests } from 'src/app/_services/helpers/apiBaseRequests';

declare let $: any;
@Component({
  selector: 'edit-children-profile',
  templateUrl: './add-child-profile.component.html',
})

export class AddChildComponent implements OnInit {

  public loading: boolean = false;
  public child: CreateChildModel = new CreateChildModel();
  public createChildErrorViewModel: CreateChildErrorViewModel = new CreateChildErrorViewModel();
  public countries: Array<Country>;
  public states: Array<State>;
  public country_USA: any;
  public USACountrySelected = false;
  public user: User;

  constructor(private apiService: ApiService,
    private toastr: ToastrManager,
    private router: Router,
    private localStorageService: LocalStorageService,
    private dataSharingService: DataSharingService,
    private apiBaseRepository: ApiBaseRequests) {
  }

  ngOnInit(): void {
    ContentHelper.setLoading(true);
    this.user = this.localStorageService.getUser();
    if (!this.user){
      this.router.navigate(['/login']);
      return;
    }

    this.countries = this.localStorageService.getCountries();
    this.states = this.localStorageService.getStates();

    if (!this.countries || !this.states) {
      this.apiBaseRepository.getCountriesStates().then(countriesStates => {
        this.countries = countriesStates.countries;
        this.states = countriesStates.states;
        this.finalizeNgOnInit();
      });
    } else {
      this.finalizeNgOnInit();
    }
  }

  private finalizeNgOnInit() {
    this.country_USA = this.countries.find(x => x.alpha2Code == AppConstants.USA_COUNTRY_ALPHA2CODE);

    var date_input = $('#dateOfBirth');
    date_input.datepicker({
      orientation: "bottom auto",
      startDate: "01/01/1990",
      endDate: "new Date()",
      startView: 2,
    });
    
    if(this.user.country){
      this.child.countryId = this.user.country.id;
    }
    if (this.user.state) {
      this.child.stateId = this.user.state.id;
    }

    this.child.gender = 0;

    date_input.change((event) => {
      this.child.dateOfBirth = date_input.val();
      this.formChangedBaseHandler()
    });

    this.onCountrySelected(this.child.countryId);
    ContentHelper.setLoading(false);
  }

  formChangedBaseHandler() {
    this.checkFormFields();
  }

  checkFormFields() {
    this.createChildErrorViewModel.setStateNoErrors();

    if (!this.child.firstName || !ValidationService.ifNameMatchPattern(this.child.firstName))
      this.createChildErrorViewModel.firstNameError = true;

    if (!this.child.lastName || !ValidationService.ifNameMatchPattern(this.child.lastName))
      this.createChildErrorViewModel.lastNameError = true;

    if (this.child.gender == undefined)
      this.createChildErrorViewModel.sexError = true;

    if (!this.child.dateOfBirth)
      this.createChildErrorViewModel.dateOfBirthError = true;

    if (!this.child.countryId)
      this.createChildErrorViewModel.countryError = true;

    if (!this.child.stateId)
      this.createChildErrorViewModel.stateError = true;

  }

  onCountrySelected(value: string) {
    this.USACountrySelected = value === this.country_USA.id;
    if (!this.USACountrySelected)
      this.child.stateId = undefined;
  }

  saveChanges() {
    this.loading = true;
    let body = ChildMapper.mapCreateChildRequest(this.child);

    if (!this.dateOfBirthIsValid(body.dateOfBirth)) {
      this.loading = false;
      this.createChildErrorViewModel.setStateErrorDateOfBirth();
      return;
    };

    this.apiService.postChild(body).subscribe((postChildResponse) => {
      this.loading = false;
      this.router.navigate(['/profile/children']);
    }, error => {
      this.loading = false;
      this.toastr.errorToastr('Error occurred!', 'Oops!');
    })
  }

  private dateOfBirthIsValid(dateOfBirth: string): boolean {
    return new Date() >= new Date(dateOfBirth);
  }
}
