import { Component, OnInit } from '@angular/core';
import { InputPatterns } from "../_models/AppConstants";
import { RestorePasswordErrorViewModel, UserRestorePasswordModel } from '../_models/restorePassword/restorePasswordModels'
import { ApiService } from '../_services/api/api.service';
import { ToastrManager } from 'ng6-toastr-notifications';
import { Router } from '@angular/router';
import { ViewChild } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';





@Component({
  selector: 'app-restore-password',
  templateUrl: './restore-password.component.html',
})
export class RestorePasswordComponent implements OnInit {
  @ViewChild('restoreResult', { static: false }) private modalChildComponent;

  public loading: boolean = false;
  public emailPattern = InputPatterns.EMAIL_PATTERN;
  public restorePasswordModel: UserRestorePasswordModel = new UserRestorePasswordModel();

  public restorePasswordErrorViewModel: RestorePasswordErrorViewModel = new RestorePasswordErrorViewModel();

  constructor(
    private apiService: ApiService,
    private toastr: ToastrManager,
    private router: Router,
    private modalService: NgbModal) { }


  ngOnInit(): void {
  }

  openModal() {
    this.modalService.open(this.modalChildComponent, { ariaLabelledBy: 'modal-basic-title', centered: true, size: 'lg' }).result.then((result) => {
      this.gotToLogin()
    }, (reason) => {
      this.gotToLogin()
    });
  }

  gotToLogin() {
    this.router.navigate(['/login']);
  }

  restorePassword() {
    this.loading = true;
    this.apiService.restorePassword(this.restorePasswordModel).subscribe((response: any) => {
      this.loading = false;
      if (response.success) {
        this.openModal();
      }
      else {
        if(response.code === 1000){
          this.toastr.errorToastr(`<b>Application error</b><br>${response.message}`, 'Oops!',  { enableHTML: true });
        }else{
          this.restorePasswordErrorViewModel.setStateErrorByResponseCode(response.code);
        }
      }
    },
      error => {
        this.toastr.errorToastr(`Error occured!`,
          'Oops!',
          { enableHTML: true, dismiss: "click", showCloseButton: true });
        this.loading = false;
      });
  }

  onChangeValue() {
    this.restorePasswordErrorViewModel.checkEmailInput(this.restorePasswordModel.email);
  }
}
