import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { RouterModule } from '@angular/router';

import { AppComponent } from './app.component';
import { HeaderStandartComponent } from './header/header-standart/header-standart.component';
import { HeaderBaseComponent } from './header/header-base/header-base.component';
import { HomeComponent } from './home/home.component';
import { FooterPrimaryComponent } from './footer/footer-primary/footer-primary.component';
import { FooterWhiteComponent } from './footer/footer-white/footer-white.component';
import { HealthTestComponent } from './health-test/health-test.component';
import { LoginComponent } from './login/login.component';
import { SignUpComponent } from './sign-up/sign-up.component';
import { RestorePasswordComponent } from './restore-password/restore-password.component';
import { LanguageSelectorComponent } from './layout/language-selector/language-selector.component';
import {NgbDropdownModule, NgbTabsetModule, NgbTooltipModule} from '@ng-bootstrap/ng-bootstrap';
import {ScriptLoaderService} from './_services/script-loader.service';
import {NgbdPopoverBasicModule} from './layout/popover/popover-basic.module';
import {DropdownComponent} from './layout/dropdown/dropdown.component';
import {AuthGuard} from './_services/guard.service';
import {LocalStorageService} from './_services/localStorage.service';
import { NgSelectModule } from '@ng-select/ng-select';
import { NgOptionHighlightModule } from '@ng-select/ng-option-highlight';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import {DataSharingService} from './_services/data-sharing.service' 
import {HealthCheckResultComponent} from './health-check-result/health-check-result.component' 
import { AuthenticationService } from './_services/auth.service'
import { RouteHandlerService } from './_services/route-handler.service'
import { ToastrModule } from 'ng6-toastr-notifications';
import { ApiService } from './_services/api/api.service';
import { AppInterceptor } from './_services/helpers/interceptor';
import { SignUpVerifiedComponent } from './sign-up-verified/sign-up-verified.component';
import { ProfileModule } from './profile/profile.module';
import { EditProfileComponent } from './edit-profile/edit-profile.component';
import { QRCodeModule } from "angularx-qrcode";
import { HashLocationStrategy, LocationStrategy } from '@angular/common';
import { PrivacyComponent } from './footer/privacy/privacy.component';
import { EditChildProfileComponent } from './child-mode/edit-child/edit-child-profile.component'
import { AddChildComponent } from './child-mode/add-child/add-child-profile.component'
import { AddChildByCodeComponent } from './child-mode/add-child-by-code/add-child-by-code.component'
import { ApiBaseRequests } from './_services/helpers/apiBaseRequests';
import { LanguageService } from './_services/language.service';
import { NewsComponent } from './news/news.component';
import { ArticleComponent } from './article/article.component';
import { DataProviderService } from './_services/data-provider/data-provider.service';
import { AngularFireModule } from '@angular/fire';
import { environment } from 'src/environments/environment';
import { AngularFirestore } from '@angular/fire/firestore';
import { AngularFireAuth } from "@angular/fire/auth";


@NgModule({
  declarations: [
    AppComponent,
    HeaderStandartComponent,
    HeaderBaseComponent,
    HomeComponent,
    FooterPrimaryComponent,
    FooterWhiteComponent,
    HealthTestComponent,
    LoginComponent,
    SignUpComponent,
    SignUpVerifiedComponent,
    RestorePasswordComponent,
    LanguageSelectorComponent,
    DropdownComponent,
    HealthCheckResultComponent,
    EditProfileComponent,
    PrivacyComponent,
    EditChildProfileComponent,
    AddChildComponent,
    AddChildByCodeComponent,
    NewsComponent,
    ArticleComponent
  ],
  imports: [
    ProfileModule,
    BrowserModule.withServerTransition({ appId: 'ng-cli-universal' }),
    HttpClientModule,
    FormsModule,
    ReactiveFormsModule,
    NgOptionHighlightModule,
    NgbModule,
    RouterModule.forRoot([
      { path: 'home', component: HomeComponent, pathMatch: 'full', canActivate: [AuthGuard]},
      { path: 'health-test', component: HealthTestComponent, canActivate: [AuthGuard] },
      { path: 'login', component: LoginComponent },
      { path: 'sign-up', component: SignUpComponent },
      { path: 'sign-up-verified', component: SignUpVerifiedComponent },
      { path: 'restore-password', component: RestorePasswordComponent },
      { path: 'health-check-result', component: HealthCheckResultComponent, canActivate: [AuthGuard] },
      { path: 'edit-profile', component: EditProfileComponent, canActivate: [AuthGuard] },
      { path: 'edit-child-profile/:id', component: EditChildProfileComponent, canActivate: [AuthGuard] },
      { path: 'add-child', component: AddChildComponent, canActivate: [AuthGuard] },
      { path: 'add-child-by-code', component: AddChildByCodeComponent, canActivate: [AuthGuard] },
      { path: 'article/:id', component: ArticleComponent},
      { path: '**', component:  NewsComponent},
    ]),
    // ng-bootstrap modules
    NgbDropdownModule,
		NgbTabsetModule,
    NgbTooltipModule,
    NgbdPopoverBasicModule,
    NgSelectModule,
    BrowserAnimationsModule,
    ToastrModule.forRoot(),
    QRCodeModule,
    AngularFireModule.initializeApp(environment.firebase),
  ],
  exports: [
    LanguageSelectorComponent,
  ],
  providers: [
    { provide: HTTP_INTERCEPTORS, useClass: AppInterceptor, multi: true },
    {provide: LocationStrategy, useClass: HashLocationStrategy},
    ScriptLoaderService, 
    AuthGuard,  
    LocalStorageService,
    LanguageService,
    DataSharingService,
    AuthenticationService,
    RouteHandlerService,
    ApiService,
    ApiBaseRequests,
    DataProviderService,
    AngularFirestore,
    AngularFireAuth
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
