import { Injectable } from "@angular/core";
import { GetCountriesStatesModel } from "src/app/_models/api/requestModels/getCountriesStatesModel";
import { Child } from "src/app/_models/children/child";
import { ApiService } from "../api/api.service";
import { LocalStorageService } from "../localStorage.service";
import { ChildMapper } from "./childMapper";

@Injectable()
export class ApiBaseRequests {

    constructor(private apiService: ApiService,
        private localStorageService: LocalStorageService) {
    }

    async getChildren() {
        let childrenResponseModel = await this.apiService.getChildren().toPromise();
        var children: Array<Child> = [];

        for (let index = 0; index < childrenResponseModel.items.length; index++) {
            let item = childrenResponseModel.items[index];
            let getUserByIdResponse = await this.apiService.getUser(item.id).toPromise();
            let child = ChildMapper.mapGetUserByIdResponseToChildModel(item.id, getUserByIdResponse);
            children.push(child);
        }
        this.localStorageService.setChildren(children);
    }

    async getCountriesStates() : Promise<GetCountriesStatesModel> {
        let response = new GetCountriesStatesModel();

        let getCountriesResponse = await this.apiService.getCountries().toPromise();
        response.countries = getCountriesResponse.items;
        this.localStorageService.setCountries(response.countries);

        let getStatesResponse = await this.apiService.getStates().toPromise();
        response.states = getStatesResponse.items;
        this.localStorageService.setStates(response.states);

        return new Promise((resolve) => {
            resolve(response);
        });
    }
}