
export class CheckModelDTO {
    id: string;
    result: number;
    timestamp: any;

    facilityId: string;
    facilityName: string;
    userId: string;
    userFirstName: string;
    userLastName: string;
}

export class Check extends CheckModelDTO {
    dateTime: any;
    dateTimeLocaleString: any;
    qrCode: QRCodeData;
  }

  export class QRCodeData {
    public qrdata: string = null;
    public elementType: "img" | "url" | "canvas" | "svg" = null;
    public level: "L" | "M" | "Q" | "H";
    public scale: number;
    public width: number;
}

export class QRCodeDataModel {
    id: string;
    f_n: string;
    r: number;
    t: number;
    n: string;
}